import { observable, action, runInAction, computed, toJS } from "mobx";
class videoMonitorStore {
  title: string = "视频监控";
  tabs: any[] = [{ title: "实时预览" }, { title: "远程回放" }];
  @observable isSearchDevice: boolean = true;
  @observable tab: number = 0;
  @observable aisleNumber: number = 1;
  @observable curSelctArea: any = { type: "realTime", i: 0 };
  @observable realTimesVideos_four: any[] = [{ isEmpty: true }, { isEmpty: true }, { isEmpty: true }, { isEmpty: true }];
  @observable realTimesVideos_max: any[] = [{ isEmpty: true }];
  @observable playBackVideo: any = { isEmpty: true };
  @observable playBackTime: any = new Date();

  @action reset = () => {
    this.isSearchDevice = true;
    this.tab = 0;
    this.aisleNumber = 1;
    this.curSelctArea = { type: "realTime", i: 0 };
    this.realTimesVideos_four = [{ isEmpty: true }, { isEmpty: true }, { isEmpty: true }, { isEmpty: true }];
    this.realTimesVideos_max = [{ isEmpty: true }];
    this.playBackVideo = { isEmpty: true };
    this.playBackTime = new Date();
  };
  @action tabChange = (index: number) => {
    this.tab = index;
  };
  @action onSearchDeviceStateChange = (bool: boolean, type?: string, i?: number) => {
    this.isSearchDevice = bool;
    this.curSelctArea = { type, i };
  };
  @action onDeviceSelectChange = (device: any) => {
    if (this.tab === 0 && this.curSelctArea && this.curSelctArea.type === "realTime") {
      if (this.aisleNumber === 1) {
        this.realTimesVideos_max = this.realTimesVideos_max.map((item, i) => {
          if (i === this.curSelctArea.i) {
            device.isEmpty = false;
            return device;
          }
          return item;
        });
      } else if (this.aisleNumber === 4) {
        this.realTimesVideos_four = this.realTimesVideos_four.map((item, i) => {
          if (i === this.curSelctArea.i) {
            device.isEmpty = false;
            return device;
          }
          return item;
        });
      }
      this.isSearchDevice = false;
    } else if (this.tab === 1) {
      device.isEmpty = false;
      this.playBackVideo = device;
      this.isSearchDevice = false;
      this.playBackTime = new Date();
    }
  };
  @action onAreaSelectChange = (selectIndex: number) => {
    if (this.aisleNumber === 1) {
      this.realTimesVideos_max = this.realTimesVideos_max.map((item, i) => {
        if (selectIndex === i) {
          let selected = !item.selected;
          return {
            ...item,
            selected
          };
        }
        return item;
      });
    } else if (this.aisleNumber === 4) {
      this.realTimesVideos_four = this.realTimesVideos_four.map((item, i) => {
        if (selectIndex === i) {
          let selected = !item.selected;
          return {
            ...item,
            selected
          };
        }
        return item;
      });
    }
  };
  @action updatePlayBackTime = (date: Date) => {
    this.playBackTime = date;
  };
  @action realTimeMax = () => {
    if (this.aisleNumber === 4) {
      let selectVideoIndex = this.realTimesVideos_four.findIndex(item => item.selected);
      if (selectVideoIndex !== -1) {
        this.realTimesVideos_max = [{ ...this.realTimesVideos_four[selectVideoIndex], selected: false }];
      }
    }
    this.aisleNumber = 1;
  };
  @action realTimeFour = () => {
    this.aisleNumber = 4;
  };
  @action realTimeRemoveSelect = () => {
    if (this.aisleNumber === 1) {
      this.realTimesVideos_max = this.realTimesVideos_max.map((item, i) => {
        if (item.selected) {
          return { isEmpty: true };
        }
        return item;
      });
    } else if (this.aisleNumber === 4) {
      this.realTimesVideos_four = this.realTimesVideos_four.map((item, i) => {
        if (item.selected) {
          return { isEmpty: true };
        }
        return item;
      });
    }
  };
  @action realTimeRemoveAll = () => {
    if (this.aisleNumber === 1) {
      this.realTimesVideos_max = [{ isEmpty: true }];
    } else if (this.aisleNumber === 4) {
      this.realTimesVideos_four = [{ isEmpty: true }, { isEmpty: true }, { isEmpty: true }, { isEmpty: true }];
    }
  };
}

export default new videoMonitorStore();
