/**
 * @description 从treeData上找到指定的节点数据
 */
interface ITreeNodeData {
    key: string
    children: ITreeNodeData[]
    [key: string]: any
}

function findNodeDataFromTreeData(treeData: ITreeNodeData[], nodeId: string): ITreeNodeData | null {
    let result: ITreeNodeData | null = null
    treeData.forEach(item => {
        if (nodeId == item.key) {
            result = item
            return false
        } else if (item.children && item.children.length > 0) {
            const res = findNodeDataFromTreeData(item.children, nodeId)
            if(res) {
                result = res
            }
        } 
    })
    return result
}

export {
    findNodeDataFromTreeData
}