/**
 * wx.chooseImage({
    count: 1, // 默认9
    sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
    sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
    defaultCameraMode: "batch", //表示进入拍照界面的默认模式，目前有normal与batch两种选择，normal表示普通单拍模式，batch表示连拍模式，不传该参数则为normal模式。（注:用户进入拍照界面仍然可自由切换两种模式）
    success: function (res) {
        var localIds = res.localIds; // 返回选定照片的本地ID列表，
                // andriod中localId可以作为img标签的src属性显示图片；
                // 而在IOS中需通过上面的接口getLocalImgData获取图片base64数据，从而用于img标签的显示
    }
});
 */
// const wx = require('../../core/jweixin-1.2.0');
type EnumSizeType = {
    original: 'original',
    compressed: 'compressed',
};
type EnumDefaultCameraMode = {
    batch: 'batch',
    normal: 'normal',
};

interface IParam extends WxEvent {
    sizeType?: EnumSizeType[],
    defaultCameraMode?: EnumDefaultCameraMode,
    sourceType: string[]
};

const API_NAME = 'chooseImage';


export default {
    getCamera: (param: IParam) => {
        window.wx.chooseImage(param);
    },
    getName: (): string => {
        return API_NAME;
    }
}
