import setConfig from './setConfig';
import onReady from './onReady';
import onError from './onError';

export default (config: IWxConfigParam) => {
    return (readyCallback?: Function, errorCallback?: ErrorCallack):void => {
        setConfig(config);
        readyCallback && onReady(readyCallback);
        errorCallback && onError(errorCallback);
    } 
}

