/**
 *  1、发起请求，获得消息列表
    2、查看公告通知详情
 */
import http from "@/utils/data-api-service";

export function getUnreadNumbers(userId: string | void | null): Promise<any> {
  const apiParam = {
    tableName: "wx_message_view",
    length: 99999,
    start: 0,
    type: "TABLE_QUERY",
    params: {
      filterCondition: [
        {
          type: "1",
          field: "wx_message_view.read_user_id",
          condition: "=",
          connection: "&amp;&amp;",
          id: 1,
          value: userId
        },
        {
          type: "1",
          field: "wx_message_view.read_status",
          condition: "=",
          connection: "&&",
          id: 2,
          value: "0"
        }
      ]
    },
    fieldName: ["wx_message_view.id", "wx_message_view.read_status"]
  };
  return new Promise((resolve, reject) => {
    http
      .request({
        url: `/api/restApi/controlData/showTableData`,
        method: "POST",
        data: apiParam
      })
      .then(res => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
}

export function getAllMessage(params: any) {
  const { start, userId } = params;
  const apiParam: IShowTableData = {
    tableName: "wx_message_view",
    length: 20,
    start: start,
    type: "TABLE_QUERY",
    params: {
      filterCondition: [
        {
          type: "1",
          field: "wx_message_view.read_user_id",
          condition: "=",
          connection: "&&",
          id: 1,
          value: userId
        }
      ],
      orderBy: [
        {
          fieldName: "wx_message_view.read_status",
          sort: "asc"
        },
        {
          fieldName: "wx_message_view.time",
          sort: "DESC"
        }
      ]
    },
    subQuery: [],
    fieldName: [
      "wx_message_view.id",
      "wx_message_view.title",
      "wx_message_view.abs_content",
      "wx_message_view.time",
      "wx_message_view.url",
      "wx_message_view.type",
      "wx_message_view.read_user_id",
      "wx_message_view.read_status",
      "wx_message_view.read_time",
      "wx_message_view.sub_type",
      "wx_message_view.source_id",
      "wx_message_view.origin_message"
    ],
    controlPermissionModelDTOs: []
  };

  return new Promise((resolve, reject) => {
    http
      .request({
        url: `/api/restApi/controlData/showTableData`,
        method: "POST",
        data: apiParam
      })
      .then(res => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
}

