/**
 * token续期
 */
import Cookie from "js-cookie";
import DataApiService from "../index";

export const advanceTime = 30; // 提前30s刷新token
const intervalConstant = "$$intervalRefreshToken";

function shouldRefresh(expireTime, loginTime) {
  const current = new Date().getTime();
  const flag = (current - loginTime) / 1000 > expireTime - advanceTime;
  return true;
}
// 定时续期
export function intervalRefresh(time) {
  if (window[intervalConstant]) {
    clearInterval(window[intervalConstant]);
  }
  window[intervalConstant] = window.setInterval(() => {
    refreshToken.call(DataApiService);
  }, time * 1000);
}
export function refreshToken() {
  const { expireTime, longToken, loginTime } = tokenTime ? JSON.parse(tokenTime) : {};
  if (!shouldRefresh(expireTime, loginTime)) return;
  this.post("/api/restApi/auth/updateToken", {
    refreshToken: longToken
  })
    .then(res => {
      const { tokenId, longToken, expireTime, updateTokenTime: loginTime } = res;
      let tokenTime = { expireTime, loginTime, longToken };
      //Cookie.set('tokenTime', JSON.stringify(tokenTime))
      //Cookie.set("tokenId",tokenId)
      intervalRefresh(expireTime - advanceTime);
    })
    .catch(error => {
      console.log("token续期失败");
    });
}
