import DataApiService from "@/utils/data-api-service";

export default function (data: any): Promise<any> {
  return new Promise((resolve, reject) => {
    DataApiService.request({
      url: "/api/restApi/videoQuery/query",
      method: "POST",
      data: { browserType: "1", videoType: "0", equipmentType: "0", ...data }
    })
      .then((res: any) => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err.toString());
      });
  });
}
