import {
    IAddDeviceFollow,
    IDeleteDeviceFollow,
    ICompositeShowTableData,
} from '../interface';

const LIMIT = 20;

const compositeShowTableData = ({
    tableName,
    filterCondition,
    fieldName,
    statisticsColumn,
}: ICompositeShowTableData) => ({
    tableName,
    length: LIMIT,
    start: 0,
    type: 'TABLE_QUERY',
    params: {
        filterCondition,
        orderBy: [],
    },
    subQuery: [],
    fieldName,
    statisticsColumn,
    controlPermissionModelDTOs: [],
});

const compositeGetDetailPageData = ({
    tableName,
    filterCondition,
    dataColumns,
}) => ({
    tableInfo: [
        {
            tableName,
            tableType: 1,
            dataColumns,
            filterCondition,
        },
    ],
    controlPermissionModelDTOs: [],
});

const compositeAddDeviceFollow = (data: IAddDeviceFollow) => ({
    tableType: '0',
    tableName: 'my_follow_defence',
    operation: 'insert',
    data: {
        my_follow_defence: [
            {
                operation: 'INSERT',
                device_id: data.device_id,
                user_id: null,
            },
        ],
    },
    isCopy: 0,
    nodeType: 0,
    systemVariable: [
        {
            columnName: 'usr_Account.id',
            variableType: 'usr_Account',
            columnField: 'my_follow_defence.user_id',
        },
    ],
});

const compositeDeleteDeviceFollow = (data: IDeleteDeviceFollow) => ({
    tableType: '1',
    tableName: 'my_follow_defence',
    ids: data.ids,
});

export {
    compositeShowTableData,
    compositeGetDetailPageData,
    compositeAddDeviceFollow,
    compositeDeleteDeviceFollow,
};
