import { findNodeDataFromTreeData } from './findNodeDataFromTreeData'

/**
 * @description 获取树选中的节点数据
 * @param isLeaf  是否过滤叶子节点  
 */

function getTreeCheckedData(treeData = [], checkedKeys: string[], isLeaf?: boolean) {
    const checkedData: any = []
    checkedKeys.forEach(item => {
        const res = findNodeDataFromTreeData(treeData, item)
        if (res) {
            if (isLeaf) {
                if (res.isLeaf) {
                    checkedData.push(res)
                }
            } else {
                checkedData.push(res)
            }
        }
    })
    return checkedData
}

export {
    getTreeCheckedData
}