import { observable, action } from "mobx";
import DataApiService, { getUserInfo, getUnreadMessageCount } from "@/service/http";
import { EnumState } from "./constance";
// import { AxiosInstance } from "axios";
import storage from "@/utils/storage";

class DashboardStore {
  tokenContext: IToken;
  webSocketUrl = "/stomp1/websocket";
  @observable modules: any = undefined;
  @observable state: any = EnumState["noop"];
  @observable children: any = null;
  @observable error: any = undefined;
  @observable photo: any = undefined;
  @observable connected: boolean = false;
  // @observable msg:object={};
  // DataApiService: AxiosInstance;

  @observable height = "20%";
  @observable unreadMessageCount = 0;

  @action
  setConnected = (bool: boolean) => {
    this.connected = bool;
  };

  @action.bound
  unreadMessageCountChange(bool: boolean) {
    this.unreadMessageCount += 1;
    console.log("更新消息徽标", this.unreadMessageCount);
  }

  @action.bound
  getUnreadMessageCount() {
    getUnreadMessageCount({ userId: storage.getItem("userId"), readStatus: 0 }).then((res: any) => {
      console.log("获取消息徽标", res.result.iTotalRecords);
      this.unreadMessageCount = res.result.iTotalRecords;
    });
  }

  @action getUserName = (): Promise<any> => {
    return new Promise((resolve, reject) => {
      getUserInfo()
        .then((res: any) => {
          if (res && res.code === 0) {
            const result = res.result;
            const baseUserInfoResult = JSON.parse(result.baseUserInfo) || {};
            const userName = baseUserInfoResult.loginname;
            resolve(userName);
          } else {
            reject(res.message);
          }
        })
        .catch(err => {
          reject(err);
        });
    });
  };
}

export default new DashboardStore();
