import DataApiService from "@/utils/data-api-service";
import getShowTableData from "../showTableData";

export const getAreasData = function (param: any) {
  return new Promise((resolve, reject) => {
    getShowTableData(param).then(res => {
      resolve(res);
    });
  });
};

export const getGroup = function (this: any, data: any): Promise<any> {
  return new Promise((resolve, reject) => {
    DataApiService.request({
      url: "/api/restApi/location/getChildData",
      method: "POST",
      data
    }).then(res => {
      resolve(res);
    });
  });
};

export const getGroupData = function (params: any) {
  return new Promise((resolve, reject) => {
    getShowTableData({
      tableName: "view_defense_area_device_total",
      start: 0,
      type: "TABLE_QUERY",
      params: {
        filterCondition: [
          {
            type: "1",
            field: "view_defense_area_device_total.groupId",
            condition: "=",
            connection: "&&",
            id: 1,
            pid: 0,
            value: params.groupId
          }
        ]
      },
      fieldName: [
        "view_defense_area_device_total.id",
        "view_defense_area_device_total.statusId",
        "view_defense_area_device_total.buildName",
        "view_defense_area_device_total.buildId"
      ],
      statisticsColumn: [
        {
          tableName: "view_defense_area_device_total",
          columnType: "GROUP_BY",
          columnName: "buildName",
          columnAlias: "view_defense_area_device_total_locationName"
        },
        {
          tableName: "view_defense_area_device_total",
          columnType: "GROUP_BY",
          columnName: "buildId",
          columnAlias: "view_defense_area_device_total_locationId"
        },
        {
          tableName: "view_defense_area_device_total",
          columnType: "COUNT",
          columnName: "id"
        },
        {
          tableName: "view_defense_area_device_total",
          columnType: "COUNT",
          columnName: "statusId"
        }
      ]
    }).then(res => {
      resolve(res);
    });
  });
};

