import baseService from './services/base';
import mediaApiService from './services/media';
import locationService from './services/location';
import geographyApiService from './services/geography';
import configure from './services/configure';
import mergeApiList from './tools/mergeApiList';

const mergeParam = [mediaApiService, geographyApiService];

const jsApiList = mergeApiList(mergeParam);

export default {
    base: baseService,
    media: mediaApiService,
    location: locationService,
    geography: geographyApiService,
    configure,
    jsApiList,
};
