/**
 * @description 使用企业微信内置地图查看位置接口
wx.openLocation({
    latitude: 0, // 纬度，浮点数，范围为90 ~ -90
    longitude: 0, // 经度，浮点数，范围为180 ~ -180。
    name: '', // 位置名
    address: '', // 地址详情说明
    scale: 1, // 地图缩放级别,整形值,范围从1~28。默认为16
});
 */
// const wx = require('../../core/jweixin-1.2.0');
interface IParam extends WxEvent {
    latitude: number,
    longitude: number,
    name: string,
    address: string,
    scale?: number
};

const API_NAME = 'openLocation';

export default {
    openLocation: (param: IParam) => {
        window.wx.openLocation(param);
    },
    getName: (): string => {
        return API_NAME;
    }
}