import http from "@/utils/data-api-service";

export const getUserInfo = function () {
  return new Promise((resolve, reject) => {
    return http
      .request({
        url: "/api/restApi/auth/getAllUserJoinCacheInfo",
        method: "GET",
      })
      .then((res: any) => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err.toString());
      });
  });
};
