import { observable, action, runInAction, computed } from "mobx";
import { getPerformPersonalCount, getUserInfo } from "@/service/http";
import storage from "@/utils/storage";
import dayjs from "dayjs";
import { Toast } from "antd-mobile";
const performCountMap = {
  view_member_executetask_detail_complete: "已履职任务数",
  view_member_executetask_detail_incomplete: "未履职任务数",
  view_member_executetask_detail_nomal: "检查项:正常数",
  view_member_executetask_detail_abnomal: "检查项:有问题数",
  view_member_executetask_detail_notChecked: "检查项:未检查数",
  view_member_executetask_detail_completionRate1: "履职完成率"
};

class PerformCountStore {
  @observable performCounts: Array<{ label: string; value: string }> = [];
  @observable endTime: string = dayjs().format("YYYY-MM");
  @observable conut = 0;
  @computed get memberId() {
    return storage.getItem("memberId");
  }
  @computed get vaildTime() {
    const maxTime = dayjs();
    if (dayjs(this.endTime, "YYYY-MM") > maxTime) {
      return false;
    }
    return true;
  }

  @action onOK = (date: Date) => {
    this.endTime = dayjs(date).format("YYYY-MM");
    if (!this.vaildTime) {
      return Toast.info("最新统计只能当前月");
    }
    this.getPerformCount();
  };
  @action
  clearData = () => {
    this.endTime = dayjs().format("YYYY-MM");
  };
  @action getPerformCount = () => {
    if (!this.memberId) {
      Toast.info("该用户未绑定人员信息");
      return;
    }
    const obj = { memberId: this.memberId, endTime: this.endTime };
    return new Promise((resolve, reject) => {
      getPerformPersonalCount(obj).then((res: any) => {
        runInAction(() => {
          if (res.result.aaData.length > 0) {
            this.performCounts = Object.keys(performCountMap).map(key => ({
              label: performCountMap[key],
              value: res.result.aaData[0][key] ? res.result.aaData[0][key] : "-"
            }));
            resolve(this.performCounts);
          } else {
            Toast.info("该用户没有这个月的履职统计数据");
            this.performCounts = Object.keys(performCountMap).map(key => ({
              label: performCountMap[key],
              value: "-"
            }));
          }
        });
      });
    });
  };
}

export default new PerformCountStore();
