/**
 * @description 打开持续定位接口 开始持续获取LBS信息
wx.invoke('startAutoLBS',{type: 'gcj02', // wgs84是gps坐标，gcj02是火星坐标
    },function(res) {
    if(res.err_msg == "startAutoLBS:ok"){
                //调用成功
    }else {
                //错误处理
    }
});
 */


enum locationType {
    wgs84 = 'wgs84',
    gcj02 = 'gcj02',
};

// const wx = require("../../core/jweixin-1.2.0");
interface IParam {
    type: locationType,
    callback: WxResponseCallBack, 
}

const API_NAME = 'startAutoLBS';
export default {
    startAutoLBS: (param: IParam) => {
        const { callback, ...invokeParams } = param;
        window.wx.invoke(API_NAME,invokeParams,callback);
    },
    getName: (): string => {
        return API_NAME;
    }
}