import http from '@/utils/data-api-service';
import { commonRuleSearch } from '@/service/http/common';
import showTableData from '@/service/http/showTableData';
import {
    CORRESPOND_AMOUNT_TREND,
    CORRESPOND_TOP10,
    CORRESPOND_PANEL_1,
    CORRESPOND_PANEL_2,
} from './constant';
import { IParams, ICorrespond } from './interface';
import dayjs from 'dayjs';
import { Result } from 'antd-mobile';

const enumType: string[] = ['day', 'month', 'year'];

/**
 * 获取所有机构数
 */
function fetchAllAgency() {
    return showTableData({
        tableName: 'org_Dept',
        start: 0,
        type: 'TABLE_QUERY',
        params: {
            orderBy: [
                {
                    fieldName: 'org_Dept.last_update_time',
                    sort: 'DESC',
                },
            ],
        },
        fieldName: ['org_Dept.id', 'org_Dept.name'],
    });
}
/***
 * 根据curpage获取对象的日期，如 2020， 2020-05， 2020-05-10
 * 时间:20200521
 * @author liuhaonan
 */
function DateForm(date:string,from:any) {
    let dateArr = date.split('-')
    return dateArr.slice(0,Math.abs(from-2))
}
/***
 * 获取值方法
 * 时间:20200521
 * @author liuhaonan
 */
function getValue(args:any,result:any) {
    let self = this
    args.forEach((name:any,index:number) => {
        self.temp1[index].value = result.aaData[0][name] || 0
    })
}

/**
 * 报警数,处置数,环比信息
 * @param type 类型，month，day，year
 * @param date 日期值，2020-12，2020-02-20，2020
 * @param deviceId 可选，设备ID或者名称（待定）
 */
function fetchPanelData1(type: string, date: string, deviceId: string) {
    let params: IParams[] = [];
    let formatStr = {
        day: 'YYYY-MM-DD',
        month: 'YYYY-MM',
        year: 'YYYY',
    };
    if (deviceId!='') {
        params.push({
            field: CORRESPOND_PANEL_1[type].deviceId,
            value: deviceId,
        });
    }
    if (enumType.indexOf(type) !== -1 && CORRESPOND_PANEL_1[type]) {
        params.push({ field: CORRESPOND_PANEL_1[type].value, value: date });
        params.push({
            field: CORRESPOND_PANEL_1[type].beforeId,
            value: dayjs(date).subtract(1, type).format(formatStr[type]),
        });
    }
    return commonRuleSearch(params, CORRESPOND_PANEL_1[type].funId);
}

/**
 * 处置类型
 * @param type
 * @param date
 * @param deviceId
 */
function fetchPanelData2(type: string, date: string, deviceId: string) {
    let params: IParams[] = [];
    if (deviceId!='') {
        params.push({
            field: CORRESPOND_PANEL_2[type].deviceId,
            value: deviceId,
        });
    }
    if (enumType.indexOf(type) !== -1 && CORRESPOND_PANEL_2[type]) {
        params.push({ field: CORRESPOND_PANEL_2[type].value, value: date });
    }
    return commonRuleSearch(params, CORRESPOND_PANEL_2[type].funId);
}
function timeHandle(date:any,num:number) {
        var date1 = new Date(date);
        var date2 = new Date(date1);  
        date2.setDate(date1.getDate()+num);  
        var time2 = date2.getFullYear()+"-"+(date2.getMonth()+1)+"-"+date2.getDate();
        return time2
}
/**
 * 获取报警统计数量趋势
 * @param type 类型，month，day，year
 * @param date 日期值，2020-12，2020-02-20，2020
 * @param deviceId 可选，设备ID或者名称（待定）
 */
function fetchAmountTrend(type: string, date: string, deviceId: string) {
    let params:any = [];
    if (deviceId!='') {
        params.push({
            field: CORRESPOND_AMOUNT_TREND[type].deviceId,
            value: deviceId,
        });
    }
    if (enumType.indexOf(type) !== -1 && CORRESPOND_AMOUNT_TREND[type]) {
        let date1 = timeHandle(date,-7);
        let date2 = timeHandle(date,7);
        let  arr = [date1,date2]
        if(type=='day'){
            ['value','againValue'].forEach((item,index) => {
                params.push({
                    field:CORRESPOND_AMOUNT_TREND[type][item],
                    value:arr[index]
                })
            })
        }else{
            params.push({
                field:CORRESPOND_AMOUNT_TREND[type].value,
                value:date
            })
        }
    }
    return commonRuleSearch(params, CORRESPOND_AMOUNT_TREND[type].funId);
}

/**
 * 获取报警统计TOP10
 * @param type 类型，month，day，year
 * @param date 日期值，2020-12，2020-02-20，2020
 * @param deviceId 可选，设备ID或者名称（待定）
 */
function fetchTop10(type: any, date: string, deviceId: string) {
    let params: IParams[] = [];
    if (deviceId) {
        params.push({
            field: CORRESPOND_TOP10[type].deviceId,
            value: deviceId,
        });
    }
    if (enumType.indexOf(type) !== -1 && CORRESPOND_TOP10[type]) {
        params.push({ field: CORRESPOND_TOP10[type].value, value: date });
    }
    return commonRuleSearch(params, CORRESPOND_TOP10[type].funId);
}

export {
    fetchAllAgency,
    fetchAmountTrend,
    fetchTop10,
    fetchPanelData1,
    fetchPanelData2,
    getValue,
    DateForm,
    timeHandle
};
