import axios from "axios";
//import addHttpHeaderField from "@/utils/tools/addHttpHeaderField";
import storage from "@/utils/storage";

const wechatAxiosInstance = axios.create({
  timeout: 15000,
  baseURL: window.baseURL || ""
});

wechatAxiosInstance.interceptors.request.use(
  config => {
    config.headers.common["Authorization"] = "Bearer " + storage.getItem("wxToken");
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

export default wechatAxiosInstance;
