// const wx = require('../../core/jweixin-1.2.0');

interface IParams extends WxEvent {
    jsApiList: string[];
}

const API_NAME = 'checkJsApi';

export default {
    checkJsApi: (params: IParams) => {
        window.wx.checkJsApi(params);
    },
    getName: (): string => {
        return API_NAME;
    },
};
