export enum EnumState {
    'noop',
    'success',
    'error',
}

export enum EnumDimension {
    'day',
    'month',
    'year',
}

export enum EnumView {
    'table',
    'chart',
}

export enum EnumOverviewType {
    'alarmNumber',
    'handleNumber',
    'falsePositiveNumber',
    'MOMRatio',
    'averageHandleTime',
    'falsePositiveRate',
}

export interface Istore {
    view: number;
    curPage: number;
    agency: IAgency[];
    overviewData: IOverview[];
    alarmTypeData: IAlarmType[];
    numberChartData: any;
    timeChartData: any;
    onSwitchView: Function;
    onPageChange: Function;
}

export interface IAgency {
    id: string;
    label: string;
    value: string;
}
export interface IOverview extends IAgency {
    type: number;
    icon?: string;
}
export interface IAlarmType extends IAgency {
    rate: number;
    index?: number;
}

export interface ITabItemProps {
    tab: number;
    view: number;
    active: boolean;
    overviewData: IOverview[];
    alarmTypeData: IAlarmType[];
    numberChartData: any;
    timeChartData: any;
    onSwitchView: Function;
}
