import { Result } from "antd-mobile";
import dayjs from "dayjs";

function formdate(date: any) {
  if (parseInt(date) <= 9) return `0${date}`;
  else return `${date}`;
}
function getMonthNum(currentDate: string) {
  return new Date(parseInt(currentDate.split("-")[0]), parseInt(currentDate.split("-")[1]), 0).getDate();
}
function toTimeType(datestr: string) {
  const temp = datestr.split("-");
  const newDate = temp.length === 3 ? new Date(datestr) : new Date(`${datestr}-01`);
  return newDate;
}
function GetDateStr(currentDate: any, AddDayCount: any) {
  const dd = currentDate instanceof Date ? currentDate : toTimeType(currentDate);
  dd.setDate(dd.getDate() + AddDayCount);
  const y = dd.getFullYear();
  const m = dd.getMonth() + 1 < 10 ? "0" + (dd.getMonth() + 1) : dd.getMonth() + 1;
  const d = dd.getDate() < 10 ? "0" + dd.getDate() : dd.getDate();
  return y + "-" + m + "-" + d;
}

function AccordingToScope(dateArr: any, scope1: any) {
  const date1 = toTimeType(scope1);
  const copryAgu = [...dateArr];
  copryAgu.map((item, index) => {
    const days = formdate(date1.getDate());
    const month = formdate(date1.getMonth() + 1);
    dateArr[index] = {
      date: `${date1.getFullYear()}-${month}-${days}`,
      value: 0
    };
    date1.setDate(date1.getDate() + 1);
    return undefined;
  });
}
function AccordingToMonth(dateArr: any, date: string) {
  const copyArr = [...dateArr];
  copyArr.map((item: any, index) => {
    const month = formdate(index + 1);
    dateArr[index] = {
      date: `${date}-${month}-01`,
      value: 0
    };
    return undefined;
  });
}
function fillArray(initArr: any, res: any) {
  res.forEach((item: any) => {
    initArr.every((childrenItem: any) => {
      const TEMP = item["2eventday"] || item["2eventmonth"];
      if (childrenItem["date"].indexOf(TEMP) !== -1) {
        childrenItem["value"] = item["21mid_count_bjsl"] || item["21mid_count_yfqs"];
        return false;
      }
      return true;
    });
  });
}
function initDateArr(type: string, res: any, currentDate: any) {
  let initArr: any;
  //初始化
  if (type === "day") {
    initArr = new Array(15);
    AccordingToScope(initArr, GetDateStr(currentDate, -7));
  }
  if (type === "month") {
    initArr = new Array(getMonthNum(currentDate));
    AccordingToScope(initArr, currentDate);
  }
  if (type === "year") {
    initArr = new Array(12);
    AccordingToMonth(initArr, currentDate);
  }
  //填充
  fillArray(initArr, res);
  return initArr;
}
export { initDateArr };
