import { observable, action, toJS, runInAction, computed } from "mobx";

import { changePassword } from "@/service/http";
import storage from "@/utils/storage";
import { Toast } from "antd-mobile";

enum vaildError {
  oldnewVaildError,
  repeatVailError,
  lengthVailError,
  contentVailError
}

class PasswordStore {
  @observable oldPassword = "";
  @observable newPassword = "";
  @observable repeatPassword = "";
  @observable loading = false;
  @action
  clearData = () => {
    this.oldPassword = "";
    this.newPassword = "";
    this.repeatPassword = "";
  };
  @computed get vaildPassword() {
    if (this.newPassword !== this.repeatPassword) {
      return vaildError.repeatVailError;
    }

    if (this.oldPassword === this.newPassword) {
      return vaildError.oldnewVaildError;
    }
    if (this.oldPassword.length < 6) {
      return vaildError.lengthVailError;
    }
    if (this.newPassword == this.repeatPassword && this.newPassword.length < 6) {
      return vaildError.lengthVailError;
    }
    if (!/[0-9]/.test(this.newPassword)) {
      return vaildError.contentVailError;
    }
    if (!/[a-z]/i.test(this.newPassword)) {
      return vaildError.contentVailError;
    }
    return true;
  }

  @action setPassword = (key: string, value: string) => {
    this[key] = value;
  };

  @action savePassword = (callback: Function) => {
    this.loading = true;
    switch (this.vaildPassword) {
      case vaildError.repeatVailError:
        this.loading = false;
        return Toast.info("新密码与确认密码输入不一致，请重新输入");
      case vaildError.oldnewVaildError:
        this.loading = false;
        return Toast.info("原密码与新密码不能一致，请重新输入");
      case vaildError.lengthVailError:
        this.loading = false;
        return Toast.info("新旧密码必须长度不小于6个字节");
      case vaildError.contentVailError:
        this.loading = false;
        return Toast.info("新密码必须包含数字和字母");
      default:
    }

    changePassword({
      password: this.oldPassword,
      clientToken: storage.getItem("client_token"),
      username: storage.getItem("userName"),
      newPassWord: this.newPassword,
      wxCard: storage.getItem("wxCard"),
      wxUser: storage.getItem("wxUser")
    })
      .then(res => {
        this.loading = false;
        if (res.code == "0") {
          Toast.success("修改成功");
          callback(storage.getItem("wxCard"), storage.getItem("wxUser"), storage.getItem("client_token"));
        } else if (res.code == "600013") {
          Toast.info("原密码输入有误");
        } else {
          let message = res.message;
          const index = message.indexOf(":");
          message = message.substring(index + 1);
          Toast.info(message);
        }
      })
      .catch(error => {
        this.loading = false;
        Toast.info("修改密码失败", 2);
      });
  };
}

export default new PasswordStore();
