// const wx = require("../../core/jweixin-1.2.0");
/**
 * @description  通过config接口注入权限验证配置
 * 所有需要使用JS-SDK的页面必须先注入配置信息，否则将无法调用（同一个url仅需调用一次，对于变化url的SPA的web app可在每次url变化时进行调用）。
wx.config({
    beta: true,// 必须这么写，否则wx.invoke调用形式的jsapi会有问题
    debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
    appId: '', // 必填，企业微信的corpID
    timestamp: , // 必填，生成签名的时间戳
    nonceStr: '', // 必填，生成签名的随机串
    signature: '',// 必填，签名，见附录1
    jsApiList: [] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
});
 */

interface IConfigParam {
  beta: boolean;
  debug?: boolean;
  appId: string;
  timeStamp: number;
  nonceStr: string;
  signature: string;
  jsApiList: string[];
}

const getConfigParam = (param: IWxConfigParam): IConfigParam => {
  const configParam = { beta: true, ...param };
  return configParam;
};

export default (param: IWxConfigParam): void => {
  const configParam = getConfigParam(param);
  window.wx.config(configParam);
};
