import { observable, action, toJS } from "mobx";
import dayjs from "dayjs";
import { initDateArr } from "@/utils/common/timeOfCompletion";
import { fetchAllAgency, getValue, fetchPanelData1, fetchPanelData2, fetchAmountTrend, fetchTop10 } from "@/service/http/statistics/alarm";
import { EnumState, EnumView, EnumOverviewType, EnumDimension, IOverview, IAlarmType } from "./constance";
import { IData as IChartLineData } from "@/components/Chart-line/constance";
class Store {
  @observable state: any = EnumState["noop"];
  @observable view: number = EnumView["table"];
  @observable curPage: number = EnumDimension["month"];
  @observable agency: Array<any> = [];
  @observable agencyObj: any;
  @observable agencyName: any = "全部机构";
  @observable currentDate: string = dayjs(new Date()).format("YYYY-MM");
  @observable copyCurrentdate: string = dayjs(new Date()).format("YYYY-MM-DD");
  @observable twoCurrentdate: any = dayjs(new Date()).format("YYYY-MM");
  @observable threeCurrentdate: any = dayjs(new Date()).format("YYYY");
  @observable overviewData: IOverview[] = [];
  @observable alarmTypeData: IAlarmType[] = [];
  @observable numberChartData: IChartLineData[] = [];
  @observable temp1: Array<any> = [
    {
      id: "1",
      label: "报警数",
      value: "12354",
      type: EnumOverviewType["alarmNumber"]
    },
    {
      id: "2",
      label: "环比",
      value: "-1.25%",
      type: EnumOverviewType["MOMRatio"]
    },
    {
      id: "3",
      label: "处置数",
      value: "9998",
      type: EnumOverviewType["handleNumber"]
    }
    // {
    //   id: "4",
    //   label: "平均处置时间",
    //   value: "0",
    //   type: EnumOverviewType["averageHandleTime"]
    // }
  ];
  @observable temp2: Array<any> = [{ id: [""], label: "全部机构", value: "" }];
  @action.bound
  onSwitchView() {
    if (this.view === EnumView["table"]) this.view = EnumView["chart"];
    else this.view = EnumView["table"];
  }
  @action changeCurpage({ formData, index }: any) {
    this.curPage = formData[index["title"]];
    if (this.curPage === 0) this.currentDate = this.copyCurrentdate;
    else if (this.curPage === 1) this.currentDate = this.twoCurrentdate;
    else if (this.curPage === 2) this.currentDate = this.threeCurrentdate;
  }
  @action.bound
  onPageChange(index: number) {
    this.saveDuplicate();
    const formData = { 月: 1, 日: 0, 年: 2 };
    this.changeCurpage({ formData, index });
    this.mediator();
  }
  @action.bound
  evaluationValue = (newValue: any) => {
    const Format = ["YYYY-MM-DD", "YYYY-MM", "YYYY"];
    newValue = dayjs(newValue).format(Format[this.curPage]);
    this.currentDate = newValue;
    this.saveDuplicate();
    this.mediator();
  };
  @action initData = () => {
    this.temp1 = [
      {
        id: "1",
        label: "报警数",
        value: "12354",
        type: EnumOverviewType["alarmNumber"]
      },
      {
        id: "2",
        label: "环比",
        value: "-1.25%",
        type: EnumOverviewType["MOMRatio"]
      },
      {
        id: "3",
        label: "处置数",
        value: "9998",
        type: EnumOverviewType["handleNumber"]
      }
      // {
      //   id: 4,
      //   label: "平均处置时间",
      //   value: "0"
      // }
    ];
    this.agency = [];
    this.agencyName = "全部机构";
    this.agencyObj = "";
    this.temp2 = [{ id: [""], label: "全部机构", value: "" }];
    this.alarmTypeData = [];
    this.numberChartData = [];
  };
  @action getAgencyseletedObj = (item: any) => {
    this.agencyObj = item;
    this.getName(item);
    return this;
  };
  @action getName = (id: any) => {
    const agencyseleted = this.agency.filter(item => {
      return item.id == id[0];
    });
    this.agencyName = agencyseleted[0]["label"];
  };
  @action getDate = () => {
    const dateType = ["day", "month", "year"];
    const date = dateType[this.curPage];
    return date;
  };
  @action saveDuplicate() {
    if (this.curPage === 0) this.copyCurrentdate = this.currentDate;
    if (this.curPage === 1) this.twoCurrentdate = this.currentDate;
    if (this.curPage === 2) this.threeCurrentdate = this.currentDate;
  }
  @action ensureFormat() {
    if (this.curPage === 0) return this.copyCurrentdate ? this.copyCurrentdate : this.currentDate;
    if (this.curPage === 1) return this.twoCurrentdate ? this.twoCurrentdate : this.currentDate;
    if (this.curPage === 2) return this.threeCurrentdate ? this.threeCurrentdate : this.curPage;
  }
  //@description 接口回调函数 liuhaonan
  @action agentCallback = res => {
    const agency = res.result.aaData.map((item: any) => ({
      id: item.org_Dept_id || "",
      value: item.org_Dept_id || "",
      label: item.org_Dept_name || ""
    }));
    this.agency = [];
    this.agency = toJS(this.agency).concat(toJS(this.temp2), agency);
  };
  @action TopTenCallback = res => {
    const filterdata = res.aaData.filter((obj: any, index: any) => {
      return index <= 9;
    });
    this.alarmTypeData = filterdata.map((item: any, index: any) => {
      return {
        id: index,
        label: item["653eventname"],
        value: item["6531mid_count_bjlx"],
        rate: item["6mid_mod_-zb"]
      };
    });
  };
  @action FuncEvent(result: any) {
    const self = this;
    switch (self.curPage) {
      case 0:
        getValue.call(self, ["1098741mid_count_brbjs", "10mid_mod_xj", "1098752mid_count_czs"], result);
        break;
      case 1:
        getValue.call(self, ["1098741mid_count_bybjs", "10mid_mod_hb", "1098752mid_count_czs"], result);
        break;
      case 2:
        getValue.call(self, ["1098761mid_count_bnbjs", "10mid_mod_hb", "1098742mid_count_yczs"], result);
        break;
    }
  }
  @action PanelCallback = (res: any) => {
    let key: number;
    const fourBorther = [{ 真实报警: "真实报警数" }, { 错误报警: "误报数" }, { 测试报警: "测试数" }, { 设备故障: "故障数" }];
    const AlarmStatistics: any = [];
    res.aaData.every((item: any) => {
      fourBorther.every((block, index) => {
        if (item["2confirmtype"] in block) {
          AlarmStatistics.push({
            id: index + 4,
            label: block[item["2confirmtype"]],
            value: item["21mid_count_czlxtj"]
          });
          return false;
        }
        return true;
      });
      if (AlarmStatistics.length === 4) {
        const sortEvent = (pro: string) => {
          return function (a: any, b: any) {
            return a[pro] - b[pro];
          };
        };
        AlarmStatistics.sort(sortEvent("id"));
        return false;
      }
      return true;
    });
    //先初始化再赋值
    this.overviewData = [];
    this.overviewData = toJS(this.overviewData).concat(this.temp1, AlarmStatistics);
    if (this.overviewData.length % 2 != 0) {
      this.overviewData.push({
        id: "",
        label: "",
        value: "",
        type: 0
      });
    }
  };
  @action AmountTrendCallback = (res: any) => {
    const time = this.getDate();
    const trueFormatDate = this.ensureFormat();
    this.numberChartData = initDateArr(time, res.aaData, trueFormatDate);
  };
  //中介者模式:处理多个接口和执行回调函数 liuhaonan
  @action mediator = () => {
    const timeer = this.currentDate || "2020-05-20";
    const date = this.getDate();
    const params = toJS(this.agencyObj) ? toJS(this.agencyObj)[0] : "";
    const syncFuncArr: any = [];
    this.configInfornation.forEach((configItem: any, index: any) => {
      if (configItem["name"]) {
        if (configItem["params"]) {
          syncFuncArr[index] = this.ApiFuncMap[configItem["name"]](date, timeer, params);
        } else {
          syncFuncArr[index] = this.ApiFuncMap[configItem["name"]]();
        }
      }
    });
    Promise.all(syncFuncArr).then(res => {
      this.configInfornation.forEach((Callback: any, index: number) => {
        Callback["callback"].call(this, res[index]);
      });
    });
  };
  //接口和回调函数数据配置模板 liuhaonan
  @observable configInfornation = [
    {
      name: "fetchAllAgency",
      callback: this.agentCallback,
      params: false
    },
    {
      name: "fetchTop10",
      callback: this.TopTenCallback,
      params: true
    },
    {
      name: "fetchPanelData1",
      callback: this.FuncEvent,
      params: true
    },
    {
      name: "fetchPanelData2",
      callback: this.PanelCallback,
      params: true
    },
    {
      name: "fetchAmountTrend",
      callback: this.AmountTrendCallback,
      params: true
    }
  ];
  //api接口映射表 liuhaonan
  @observable ApiFuncMap = {
    fetchAllAgency: fetchAllAgency,
    fetchTop10: fetchTop10,
    fetchPanelData1: fetchPanelData1,
    fetchPanelData2: fetchPanelData2,
    fetchAmountTrend: fetchAmountTrend
  };
}
export default new Store();
