// import {IShowTableDataParams} from './interface';
import DataApiService from "@/utils/data-api-service";
export default function (param: any) {
  return new Promise((resolve, reject) => {
    DataApiService.request({
      url: "/api/restApi/pageControl/insertBusinessData",
      method: "POST",
      data: param,
    })
      .then((res) => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
}
