import http from "@/utils/data-api-service";

export default function (): Promise<any> {
  return new Promise((resolve, reject) => {
    http
      .request({
        url: "/doWeChat/weChat/access/getWeChat",
        method: "GET"
      })
      .then((res: IHttpTicketResponse) => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
}
