import jssdk from "@/utils/wx";
import getSignature from "./signature/getSignature";
import httpSignatureFactory from "./signature/factories/httpSignatureFactory"; // 这是一个async方法
import React from "react";
import cryptoRandomString from "crypto-random-string";
import sha1 from "crypto-js/sha1";
import { Toast } from "antd-mobile";

/**
 * @description 介绍微信jssdk的使用步骤
 * 1. 获取jssdk的核心文件
 * 2. 通过config接口注入权限验证配置
 * 3. 通过ready接口处理成功验证， 通过error接口处理失败验证
 * 4. 判断当前客户端版本是否支持指定JS接口
 * 5. 调用项目封装后的js接口
 */
const WxResource = require("@/resource/wx.json");
const { getWeChatConfig } = require("@/service/http");

interface ISignatureParam {
  timestamp: number;
  noncestr: string;
  url: string;
  jsapi_ticket: string;
}

interface IConfigParam {
  signature: string;
  timeStamp: number;
  nonceStr: string;
}

const _getSignature = (param: ISignatureParam): string => {
  const queue = ["jsapi_ticket", "noncestr", "timestamp", "url"];
  const queueBeforeStr = queue.reduce((str: string, newValue: string) => {
    const value = param[newValue] ? param[newValue] : "";
    return `${str}${newValue}=${value}&`; //jsapi_ticket=JSAPITICKET&
  }, "");
  // alert(queueBeforeStr);
  const queueStr = queueBeforeStr.substr(0, queueBeforeStr.length - 1);
  const signature = sha1(queueStr);
  return signature.toString();
};

const _getConfig = (wxApi: any, param: IConfigParam): any => {
  const { signature, nonceStr, timeStamp } = param;

  return new Promise((resolve, reject) => {
    getWeChatConfig()
      .then((res: any) => {
        const { result } = res;
        resolve({
          // debug: true,
          appId: result.corp_id,
          timestamp: timeStamp,
          nonceStr,
          signature,
          jsApiList: wxApi.jsApiList
        });
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
};

const _getTimeStamp = (): number => {
  const time = new Date().getTime();
  const timeStamp = Math.floor(time / 1000);
  return timeStamp;
};

const run = (url: string, readyCallback?: Function, errorCallback?: ErrorCallack) => {
  const wxApi = jssdk;
  getSignature(httpSignatureFactory).then((jsapi_ticket: string) => {
    const timeStamp = _getTimeStamp();
    const nonceStr = cryptoRandomString({ length: 10 });
    const signature = _getSignature({
      timestamp: timeStamp,
      noncestr: nonceStr,
      url,
      jsapi_ticket
    });

    _getConfig(wxApi, { signature, nonceStr, timeStamp })
      .then((config: any) => {
        // alert(JSON.stringify(config));
        const configure = wxApi.configure(config);
        configure(readyCallback, errorCallback);
      })
      .catch(() => {
        Toast.info("获取微信配置失败");
      });
  });
};

export const getWxApi = (url: string, onload?: Function) => {
  const readyCallback = () => {
    // TODO 如果有js api需要在页面加载时调用，需要在此回调里执行。
    // alert("333");
    onload && onload();
  };

  const errorCallback = () => {
    // TODO 异常处理，比如重新发起请求更新权限签名
    // alert("1212");
    // run();
  };

  run(url, readyCallback, errorCallback);

  return jssdk;
};

const WxApiContext = React.createContext<Partial<WxContextProps>>({});

export default WxApiContext;
