import { observable, action, runInAction, computed } from "mobx";
import { getPerformDetailView } from "@/service/http";
import { Toast } from "antd-mobile";
import storage from "@/utils/storage";
type RParam = {
  dotype: string;
  startTime: string;
  endTime: string;
  userId: string;
  start: number;
  length: 10;
};
export type IPerformDetailViewItem = {
  vExecuteMg_Deal_Auto_Detail_id: string;
  ExecuteMg_Deal_Auto_Detail_deviceName: string;
  ExecuteMg_Deal_Auto_Detail_deviceLocation: string;
  ExecuteMg_Deal_Auto_Detail_operateTime: string;
};
class PerformDetailViewStore {
  @observable viewList: Array<IPerformDetailViewItem> = [];
  @observable isLoading = false;
  @observable hashMore = true;
  @observable rParam: RParam = {
    dotype: "",
    startTime: "",
    endTime: "",
    userId: "",
    start: 0,
    length: 10
  };
  @observable refreshing = false;
  @observable pageIndex = 0;
  @observable total = 0;
  @observable dataSource: any[] = [];
  @computed get pageNumber() {
    return (this.total - 1) / this.rParam.length + 1;
  }
  @action pageIndexrest = () => {
    this.pageIndex = 0;
    this.dataSource = [];
  };
  @action onRefresh = () => {
    this.pageIndexrest();
    this.performDetailViewFetch();
  };
  @action initStore = (state: RParam) => {
    this.rParam.dotype = state.dotype;
    this.rParam.endTime = state.endTime;
    this.rParam.startTime = state.startTime;
    this.rParam.userId = state.userId;
    this.pageIndexrest();
    this.performDetailViewFetch();
  };
  @action
  clearData = () => {
    this.viewList = [];
  };
  @action performDetailViewFetch = () => {
    this.pageIndex += 1;
    this.rParam.start = (this.pageIndex - 1) * this.rParam.length;
    this.isLoading = true;
    getPerformDetailView(this.rParam).then((res: any) => {
      runInAction(() => {
        this.total = res.result.iTotalRecords;
        if (res.result.iTotalDisplayRecords > 0) {
          this.dataSource = this.dataSource.concat(res.result.aaData);
          // this.viewList = res.result.aaData;
        } else if (this.dataSource.length == 0) {
          Toast.info("履职详情为空");
        }
        if (this.pageIndex > this.pageNumber) {
          this.hashMore = false;
        }
        this.isLoading = false;
      });
    });
  };
}

export default new PerformDetailViewStore();
