import { observable, action, toJS, runInAction, computed } from "mobx";
import storage from "@/utils/storage";
import { getPersonal, QuitSys } from "@/service/http";
import socket from "@/pages/_internal/app/middlewares/AppAuthorized/websocket";
import { Toast } from "antd-mobile";

class PersonalStore {
  @observable name: string = "";
  @observable orgName: string = "";

  @computed get userId() {
    return storage.getItem("userId");
  }

  @action
  getUserInfo() {
    getPersonal({ userId: this.userId }).then((res: any) => {
      runInAction(() => {
        this.name = res.result.aaData[0]["yonghujigoubiao_v_2$Name"];
        this.orgName = res.result.aaData[0]["yonghujigoubiao_v_3$name"];
      });
    });
  }
  @action
  QuitSys = (callback: Function) => {
    let userToken = storage.getItem("userToken") ? storage.getItem("userToken") : "";
    QuitSys(userToken).then(res => {
      if (res.code != "0") {
        let index = res.message.indexOf(":");
        let message = res.message.substring(index + 1);
        Toast.info(message);
      } else if (res.code == "0") {
        callback(res.result);
      }
      setTimeout(() => {
        socket.close();
        this.clearData();
        storage.clear();
      }, 1000);
    });
  };
  @action
  clearData = () => {
    this.name = "";
    this.orgName = "";
  };
}

export default new PersonalStore();
