import { IGetDetailPageData } from "./interface";
import DataApiService from "@/utils/data-api-service";
export default function (this: any, params: IGetDetailPageData) {
  return new Promise((resolve, reject) => {
    DataApiService.request({
      url: `/api/restApi/controlData/getDetailPageData`,
      method: "POST",
      data: params,
    })
      .then((res) => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
}
