import DataApiService from "@/utils/data-api-service";
export default function (data: any): Promise<any> {
  return new Promise((resolve, reject) => {
    DataApiService.request({
      url: "/api/restApi/replayServer/info",
      method: "POST",
      data
    })
      .then(res => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
}
