import http from "@/utils/data-api-service";
import CryptoJS from "crypto-js";
import storage from "@/utils/storage";
const encrypt = function (word: any, keyStr: any) {
  const key = CryptoJS.enc.Utf8.parse(keyStr);
  const srcs = CryptoJS.enc.Utf8.parse(word);
  const encrypted = CryptoJS.AES.encrypt(srcs, key, {
    mode: CryptoJS.mode.ECB,
    padding: CryptoJS.pad.Pkcs7
  });
  return encrypted.toString();
};
export function getPersonal(params: any) {
  const { userId } = params;
  const apiParam: IShowTableData = {
    tableName: "yonghujigoubiao",
    length: 10,
    start: 0,
    type: "TABLE_QUERY",
    params: {
      filterCondition: [
        {
          field: "yonghujigoubiao.v_1$id",
          condition: "=",
          connection: "",
          id: 1,
          pid: 0,
          value: userId
        }
      ],
      orderBy: []
    },
    fieldName: ["yonghujigoubiao.v_1$id", "yonghujigoubiao.v_2$Name", "yonghujigoubiao.v_3$name"],
    controlPermissionModelDTOs: []
  };
  return new Promise((resolve, reject) => {
    http
      .request({
        url: `/api/restApi/controlData/showTableData`,
        method: "POST",
        data: apiParam
      })
      .then(res => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
}

export function changePassword(param: any): Promise<any> {
  param.password = encrypt(param.username + param.password, "flowhysuseraesco");
  param.newPassWord = encrypt(param.username + param.newPassWord, "flowhysuseraesco");
  return new Promise((resolve, reject) => {
    http
      .request({
        url: "/doWeChat/weChat/login/changePassword",
        method: "POST",
        data: param
      })
      .then((res: any) => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err.toString());
      });
  });
}
type Header = {
  clientToken?: string;
  Authorization?: string;
};
export function QuitSys(param: string, type?: number) {
  let headers: Header = {
    clientToken: storage.getItem("client_token")
  };
  if (type == 0) {
    headers = {
      clientToken: storage.getItem("client_token"),
      Authorization: "Bearer " + storage.getItem("Authorization")
    };
  }
  return new Promise((resolve, reject) => {
    http
      .request({
        headers: headers,
        url: `/doWeChat/weChat/login/userSignOut?userToken=${param}`,
        method: "GET"
      })
      .then((res: any) => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err.toString());
      });
  });
}
