import React from "react";
import { BrowserRouter as Router } from "react-router-dom";
import Routes from "./routes";
import stores from "../stores";
import { createBrowserHistory } from "history";
import Store from "./store";
import { Provider } from "mobx-react";

// eslint-disable-next-line @typescript-eslint/no-var-requires
const packageJSON = require("../../package.json");
const projectName = packageJSON["projectName"];
const project = projectName ? (window.baseURL || "") + "/" + projectName : "/";
// import DataServiceApi from 'data-api-service'
/**
 * @description
 * 这是项目路由的入口文件，路由以react 组件的形式提供导航功能。
 * 路由除入口外还有两个部分，分别是:目标组件 以及 路由参数
 */
// import AboutRoute from "./routes/AboutRoute";
// import HomeRoute from './routes/HomeRoute';
// import Home from "@/pages/Home";
// import About from "@/pages/About";

const history = createBrowserHistory({
  basename: project
});

export const HistoryContext = React.createContext({ history });
export type Stores = typeof stores;
history.listen(location => {
  // console.log("location.pathname", location.pathname); // /home
});

export const historyStore = new Store({ history });

const Route = (): ReactElement => {
  return (
    <Provider {...stores}>
      <HistoryContext.Provider value={{ history }}>
        <Router basename={`${window.baseURL || ""}/EnterpriseWXHYSUI`}>
          <Routes />
        </Router>
      </HistoryContext.Provider>
    </Provider>
  );
};

export default Route;
