import React, { ReactElement } from "react";
import { Route, Switch, Redirect, RouteProps } from "react-router-dom";
import NotFound from "@/components/NotFound";
import Loading from "@/components/Loading";

const { lazy, Suspense } = React;

const Dashboard = lazy(() => import(/* webpackChunkName:"dashboard" */ "@/pages/dashboard"));

const Login = lazy(() => import(/* webpackChunkName:"login" */ "@/pages/login"));

const PerformanceManagement = lazy(() => import(/* webpackChunkName:"performanceManagement" */ "@/pages/Perform/PerformanceManagement"));

const PerformanceManagementDetail = lazy(
  () => import(/* webpackChunkName:"performanceManagementDetail" */ "@/pages/Perform/PerformanceManagementDetail")
);
const SelectImage = lazy(() => import(/* webpackChunkName:"performanceManagementSelectImg" */ "@/pages/select-image"));
const DashMessage = lazy(() => import(/* webpackChunkName:"dashMessage" */ "@/pages/Messages"));

const DashNotice = lazy(() => import(/* webpackChunkName:"dashNoticeDetail" */ "@/pages/Messages/NoticeDetail"));

const VideoMonitor = lazy(() => import(/* webpackChunkName:"videoMonitor" */ "@/pages/videoMonitor"));

const Protection = lazy(() => import(/* webpackChunkName:"protectionHome" */ "@/pages/protection/home"));

const ProtectionDetail = lazy(() => import(/* webpackChunkName:"protectionDetail" */ "@/pages/protection/detail"));

const Follow = lazy(() => import(/* webpackChunkName:"follow" */ "@/pages/follow"));

const AlarmStatistics = lazy(() => import(/* webpackChunkName:"alarmStatistics" */ "@/pages/alarm-statistics"));

const PerformStatistics = lazy(() => import(/* webpackChunkName:"performStatistics" */ "@/pages/Perform/Statistics"));

const MeetingNotification = lazy(() => import(/* webpackChunkName:"meetingNotification" */ "@/pages/meeting/notification/meetingNotification"));

//Todo
const MeetingNotificationDetail = lazy(
  () => import(/* webpackChunkName:"meetingNotificationDetail" */ "@/pages/meeting/notification/meetingNotificationDetail")
);

const UserBinding = lazy(() => import(/* webpackChunkName:"userBinding" */ "@/pages/user-binding"));

const Process = lazy(() => import(/* webpackChunkName:"process" */ "@/pages/process"));

const MeetingManagement = lazy(() => import(/* webpackChunkName:"meetingManagement" */ "@/pages/meeting/management"));

const MeetingManagementDetail = lazy(() => import(/* webpackChunkName:"meetingManagementDetail" */ "@/pages/meeting/management/detailV2"));

const SelectMeetingRoom = lazy(() => import(/* webpackChunkName:"meetingManagementSelectRoom" */ "@/pages/meeting/management/selectRoomV2"));

const Personal = lazy(() => import(/* webpackChunkName:"dashPersonal" */ "@/pages/Personal"));

const Password = lazy(() => import(/* webpackChunkName:"Password" */ "@/pages/Personal/Password"));

const ChooseAddress = lazy(() => import(/* webpackChunkName:"chooseAddress" */ "@/pages/chooseAddress"));

const ChoosePerson = lazy(() => import(/* webpackChunkName:"choosePerson" */ "@/pages/choosePerson"));

const ChoosePersonState = lazy(() => import(/* webpackChunkName:"choosePerson" */ "@/pages/choosePerson/state"));

const RemovePerson = lazy(() => import(/* webpackChunkName:"removePerson" */ "@/pages/choosePerson/removePerson"));

const PerformDetailView = lazy(
  () => import(/* webpackChunkName:"performDetailView" */ "@/pages/Perform/PerformanceManagementDetail/performDetailView")
);

const PerformCount = lazy(() => import(/* webpackChunkName:"performCount" */ "@/pages/Perform/PerformCount"));

const FlowShell = lazy(() => import(/* webpackChunkName:"flowShell" */ "@/pages/flow-shell"));

const FileView = lazy(() => import(/* webpackChunkName:"fileView" */ "@/pages/file-view"));
const FlowTodo = lazy(() => import(/* webpackChunkName:"flowTodo" */ "@/pages/flow-todo"));

const Result = lazy(() => import(/* webpackChunkName:"resultPage" */ "@/pages/result"));
export const routes: RouteProps[] = [
  {
    path: "/dashboard",
    exact: true,
    component: Dashboard
  },

  {
    path: "/login",
    exact: true,
    component: Login
  },

  {
    path: "/user-binding",
    exact: true,
    component: UserBinding
  },

  {
    path: "/performance_management",
    exact: true,
    component: PerformanceManagement
  },

  {
    path: "/perform_detail",
    exact: true,
    component: PerformanceManagementDetail
  },

  {
    path: "/select-image",
    exact: true,
    component: SelectImage
  },

  {
    path: "/dash_message",
    exact: true,
    component: DashMessage
  },

  {
    path: "/dash_notice",
    exact: true,
    component: DashNotice
  },

  {
    path: "/video-monitor",
    exact: true,
    component: VideoMonitor
  },

  {
    path: "/protection",
    exact: true,
    component: Protection
  },

  {
    path: "/protection/detail/:id",
    exact: true,
    component: ProtectionDetail
  },

  {
    path: "/follow",
    exact: true,
    component: Follow
  },

  {
    path: "/alarm-statistics",
    exact: true,
    component: AlarmStatistics
  },

  {
    path: "/perform-statistics",
    exact: true,
    component: PerformStatistics
  },

  {
    path: "/meeting-notification",
    exact: true,
    component: MeetingNotification
  },

  {
    path: "/meeting-notification/:id",
    exact: true,
    component: MeetingNotificationDetail
  },

  {
    path: "/meeting-management",
    exact: true,
    component: MeetingManagement
  },

  {
    path: "/meeting-management/:type/:id",
    exact: true,
    component: MeetingManagementDetail
  },

  {
    path: "/meeting-selectRoom",
    exact: true,
    component: SelectMeetingRoom
  },

  {
    path: "/process",
    exact: true,
    component: Process
  },

  {
    path: "/choose_address",
    exact: true,
    component: ChooseAddress
  },

  {
    path: "/choose_person",
    exact: true,
    component: ChoosePerson
  },
  {
    path: "/remove_person",
    exact: true,
    component: RemovePerson
  },
  {
    path: "/choose_person/state",
    exact: true,
    component: ChoosePersonState
  },
  {
    path: "/perform_count",
    exact: true,
    component: PerformCount
  },
  {
    path: "/perform_detail_view",
    exact: true,
    component: PerformDetailView
  },
  {
    path: "/personal",
    exact: true,
    component: Personal
  },
  {
    path: "/password",
    exact: true,
    component: Password
  },
  {
    path: "/flow-shell/:id/:name",
    exact: true,
    component: FlowShell
  },
  {
    path: "/file-view",
    exact: true,
    component: FileView
  },
  {
    path: "/flow-todo",
    exact: true,
    component: FlowTodo
  },
  {
    path: "/result",
    exact: true,
    component: Result
  },
  {
    path: "*",
    component: NotFound
  }
];

const Routes = (): ReactElement => (
  <Suspense fallback={<Loading />}>
    <Switch>
      {routes.map(r => {
        const { path, exact, component } = r;
        const LazyCom: any = component;
        return <Route key={path + ""} exact={exact} path={path} render={(props: any) => <LazyCom {...props} />} />;
      })}
    </Switch>
  </Suspense>
);

export default Routes;
