import { observable, action, runInAction, computed } from "mobx";
// import { ITabs } from './interface';
import { fetchMeetingManagement } from "@/service/http/meeting/management";
import { removePrefix } from "@/utils/common";
import dayjs from "dayjs";
class meetingManagementStore {
  //   @observable isRoute:boolean = false
  title: string = "会议管理";
  tabs: GTabs[] = [{ title: "未进行" }, { title: "进行中" }, { title: "已结束" }];
  notStartedTimeRange_default: Date[] = [dayjs().startOf("day").toDate(), dayjs().add(7, "day").endOf("day").toDate()];
  processingTimeRange_default: Date[] = [dayjs().startOf("day").toDate(), dayjs().endOf("day").toDate()];
  overTimeRange_default: Date[] = [dayjs().subtract(7, "day").startOf("day").toDate(), dayjs().endOf("day").toDate()];
  @observable curTab: number = 0;
  @observable notStartedTimeRange: Date[] = this.notStartedTimeRange_default;
  @observable processingTimeRange: Date[] = this.processingTimeRange_default;
  @observable overTimeRange: Date[] = this.overTimeRange_default;

  @action reset = () => {
    this.curTab = 0;
    this.notStartedTimeRange = this.notStartedTimeRange_default;
    this.processingTimeRange = this.processingTimeRange_default;
    this.overTimeRange = this.overTimeRange_default;
  };

  @action onTabChange = (tab: GTabs, index: number) => {
    this.curTab = index;
  };

  @action changeNotStartedTimeRange = (dateArr: Date[]) => {
    this.notStartedTimeRange = dateArr;
  };

  @action changeProcessingTimeRange = (dateArr: Date[]) => {
    this.processingTimeRange = dateArr;
  };

  @action changeOverTimeRange = (dateArr: Date[]) => {
    this.overTimeRange = dateArr;
  };

  @action getMeeting = (skip: number, limit: number, params: any) => {
    return new Promise((resolve, reject) => {
      fetchMeetingManagement({ skip, limit, filterCondition: params.filterCondition, orderBy: params.orderBy || null })
        .then((res: any) => {
          const { result, success } = res;
          if (success) {
            resolve({
              data: removePrefix("meeting_info_wx_view", result.aaData),
              total: result.iTotalRecords
            });
          } else {
            reject(new Error("请求出错"));
          }
        })
        .catch(err => {
          reject(new Error(err));
        });
    });
  };
}

export default new meetingManagementStore();
