import getDetailPageData from "../getDetailPageData";
import DataApiService from "@/utils/data-api-service";

export const fetchDetail = function (param: any) {
  return new Promise((resolve, reject) => {
    getDetailPageData(param)
      .then((res) => {
        resolve(res);
      })
      .catch((err) => {
        reject(err);
      });
  });
};

export const meetingSignInCheck = function (params: any) {
  return new Promise((resolve, reject) => {
    DataApiService.request({
      url: "/api/restApi/meeting/check_sign_user",
      method: "POST",
      data: params,
    })
      .then((res) => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
};

export const meetingSignIn = function (data: any) {
  return new Promise((resolve, reject) => {
    DataApiService.request({
      url: "/api/restApi/meeting/sign",
      method: "POST",
      data,
    })
      .then((res) => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
};
