import http from "@/utils/data-api-service";
import { removePrefix } from "@/utils/common";
import { IShowTableData } from "./interface/showTableData";
import { IGetDetailPageData } from "./interface/getDetailPageData";

/**
 * 通用接口 https://www.tapd.cn/67728721/markdown_wikis/?#1167728721001005216
 */

/**
 * 通用接口获取列表
 * @param params
 * tableName: 表名
 * skip: 跳过
 * length: 每页长度
 * filterCondition: 条件，参照文档
 * fields: 所需要的表列名（字符串数组）
 * orderBy: 排序方式，参照文档和通用接口
 */
function showTableData(): Promise<any> {
  const temp = {};
  temp["filterCondition"] = this.params.filterCondition || [];
  temp["orderBy"] = this.params.orderBy || [];
  const self = this;
  const data: IShowTableData = {
    tableName: this.tableName,
    start: this.start,
    type: this.type || "TABLE_QUERY",
    params: temp,
    fieldName: this.fieldName,
    funid: this.funid || "",
    controlPermissionModelDTOs: this.controlPermissionModelDTOs || []
  };
  return new Promise((resolve, reject) => {
    http
      .request({
        url: "/api/restApi/controlData/showTableData",
        method: "POST",
        data
      })
      .then((res: any) => {
        const { data = {} } = res;
        const { result, success } = res;
        if (success) {
          result.aaData = removePrefix(self.tableName, result.aaData);
          resolve(result);
        } else {
          reject(new Error("请求出错"));
        }
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
}

/**
 * 通用接口获取详情
 * @param params
 * tableName:"表名"
 * columns:"所需字段字符串数组"
 * params:"条件，通常为ID {id:'123456'}
 */
function getDetailPageData({ tableName, columns, params }: any): Promise<any> {
  const data: IGetDetailPageData = {
    tableInfo: [
      {
        tableName,
        tableType: 1,
        dataColumns: columns.map((item: string) => ({
          columnName: item
        })),
        filterCondition: Object.keys(params).map((field: any) => ({
          field,
          type: 3,
          value: params[field]
        }))
      }
    ]
  };

  return new Promise((resolve, reject) => {
    http
      .request({
        url: "/api/restApi/controlData/getDetailPageData",
        method: "POST",
        data
      })
      .then((data: any) => {
        const { result, success } = data;
        if (success && result && result.dataJson && result.dataJson[tableName][0]) {
          resolve(result.dataJson[tableName][0]);
        } else {
          reject(new Error("请求出错"));
        }
      })
      .catch((err: Error) => {
        reject(err.toString());
      });
  });
}

/**
 * 通用文件上传
 * @param data formData
 */
function pageFileUpload(data: any): Promise<any> {
  return new Promise((resolve, reject) => {
    http
      .request({
        url: "/api/pageFileUpload",
        method: "POST",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        timeout: 500000,
        data
      })
      .then((res: any) => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err.toString());
      });
  });
}

/**
 * 通用规则搜索
 * @param params field value
 * @param funId 搜索规则
 */
function commonRuleSearch(params: any[], funId: string) {
  const params_fixed = {
    pid: 0,
    connection: "and",
    condition: ":",
    type: "ES_DATA_PAGE",
    allSearch: false
  };
  const filterCondition: any = params.map(({ field, value }) =>
    Object.assign({}, params_fixed, {
      field,
      value
    })
  );

  return showTableData.apply({
    tableName: "MissTableName",
    start: 0,
    type: "ES_DATA_PAGE",
    params: {
      filterCondition
    },
    fieldName: ["MissTableName.id"],
    funid: funId
  });
}

export { showTableData, getDetailPageData, pageFileUpload, commonRuleSearch };
