/* eslint-disable @typescript-eslint/no-inferrable-types */
import { observable, action, runInAction, computed, observe } from "mobx";
import { getPerformList, getUserInfo } from "@/service/http";
import dayjs from "dayjs";
import storage from "@/utils/storage";
import { Toast } from "antd-mobile";
type IApiParams = {
  status: string;
  startTime: string;
  endTime: string;
  start: number;
  length: number;
  personID: string;
};

class PerformanceManagementStore {
  props: any;
  constructor(props: any) {
    this.props = props;
    this.initStore(props.status);
  }
  dataSource: any[] = [];
  @observable isLoading: boolean = false;
  @observable hasMore: boolean = true;
  @observable apiParams: IApiParams = {
    status: "0",
    startTime: dayjs().subtract(1, "month").format("YYYY-MM-DD"),
    endTime: dayjs().format("YYYY-MM-DD"),
    start: 0,
    length: 20,
    personID: ""
  };
  @observable refreshing: boolean = false;
  @observable pageIndex: number = 0;
  @observable total: number = 0;
  @computed get pageNumber() {
    return (this.total - 1) / this.apiParams.length + 1;
  }
  @computed get personID() {
    const memberId = storage.getItem("memberId");
    return memberId;
  }

  @action pageIndexrest = () => {
    this.pageIndex = 0;
    this.dataSource = [];
  };

  @action initStore = (status: string) => {
    this.apiParams.status = status;
    this.pageIndexrest();
    this.performFetch();
  };

  @action onSearch = (date: [Date, Date]) => {
    if (!this.personID) {
      return Toast.info("该用户未绑定人员信息");
    }
    const [startTime, endTime] = date;
    this.apiParams = {
      status: this.apiParams.status,
      startTime: dayjs(startTime).format("YYYY-MM-DD"),
      endTime: dayjs(endTime).format("YYYY-MM-DD"),
      start: 0,
      length: 20,
      personID: this.personID
    };
    this.dataSource = [];
    this.onRefresh();
  };

  @action onRefresh = () => {
    this.pageIndexrest();
    this.performFetch();
  };
  @action performFetch = (callback?: Function) => {
    if (!this.personID) {
      return Toast.info("该用户未绑定人员信息");
    }
    this.pageIndex += 1;
    this.apiParams.start = (this.pageIndex - 1) * this.apiParams.length;
    this.apiParams.personID = this.personID;
    this.isLoading = true;
    getPerformList(this.apiParams).then((res: any) => {
      runInAction(() => {
        this.total = res.result.iTotalRecords;
        if (this.props.setLenth) {
          this.props.setLenth(this.total);
        }
        this.dataSource = this.dataSource.concat(res.result.aaData);
        if (this.pageIndex > this.pageNumber) {
          this.hasMore = false;
        }
        this.isLoading = false;
        callback && callback();
      });
    });
  };
}

export default PerformanceManagementStore;
