import { IRes, IParam } from "./interface";
import DataApiService from "@/utils/data-api-service";
export default function (param: IParam) {
  return new Promise((resolve, reject) => {
    DataApiService.request({
      url: "/api/restApi/menu/searchList",
      method: "POST",
      data: param,
    })
      .then((res) => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err.toString());
      });
  });
}
