/**
 * @description 停止持续定位接口
 */

// const wx = require("../../core/jweixin-1.2.0");

const API_NAME = 'stopAutoLBS';


export default {
    stopAutoLBS: (callback: WxResponseCallBack):void => {
        window.wx.invoke(API_NAME, callback);
    },
    getName: (): string => {
        return API_NAME;
    }
}