import { observable, action, runInAction, computed } from "mobx";
// import { ITabs } from './interface';
// import { fetchMeetingManagement } from '@/service/http/meeting/management';
import { performCompletionRate, performQuality } from "@/service/http/statistics/perform";
import dayjs from "dayjs";
import { Toast } from "antd-mobile";
class meetingManagementStore {
  //   @observable isRoute:boolean = false
  title = "履职管理";
  @observable month: string = dayjs().format("YYYY-MM");
  @observable table: any[] = [];
  @computed get vaildTime() {
    const maxTime = dayjs();
    if (dayjs(this.month, "YYYY-MM") >= maxTime) {
      return false;
    }
    return true;
  }

  @action reset = () => {
    this.month = dayjs().format("YYYY-MM");
    this.table = [];
    this.chartData = [];
  };

  @action onOK = (date: Date) => {
    this.month = dayjs(date).format("YYYY-MM");
    if (!this.vaildTime) {
      this.chartData = [];
      this.table = [];
      return Toast.info("最新统计只能统计当前月");
    }
    this.getTableData();
  };
  @action
  getTableData = () => {
    performCompletionRate(this.month).then(res => {
      this.table = res.aaData;
    });
  };
  @observable chartData: Array<any> = [];
  @action
  getCharData = () => {
    return new Promise((resolve, reject) => {
      performQuality(this.month).then((res: any) => {
        const arr: any = [];
        if (Array.isArray(res.aaData)) {
          res.aaData.map((item: any, i: any) => {
            const obj1 = {
              label: "",
              type: "",
              value: ""
            };
            obj1.label = item.id_JSON.name;
            obj1.value = item.nomal ? item.nomal : 0;
            obj1.type = "正常";
            arr.push(obj1);
            const obj2 = {
              label: "",
              type: "",
              value: ""
            };
            obj2.label = item.id_JSON.name;
            obj2.value = item.abnomal ? item.abnomal : 0;
            obj2.type = "有问题";
            arr.push(obj2);
            const obj3 = {
              label: "",
              type: "",
              value: ""
            };
            obj3.label = item.id_JSON.name;
            obj3.value = item.notChecked ? item.notChecked : 0;
            obj3.type = "未检查";
            arr.push(obj3);
          });
          runInAction(() => {
            this.chartData = arr;
            resolve(true);
          });
        } else {
          reject(new Error("请求失败"));
        }
      });
    }).then(res => {
      console.log();
    });
  };
}

export default new meetingManagementStore();
