let timer: NodeJS.Timeout;

export const expireListener = (expireSecond: number, handler: () => void) => {
  if (!expireSecond || expireSecond < 60) {
    expireSecond = 60;
  }
  clearTimeout(timer);
  timer = setTimeout(() => {
    handler && handler();
  }, expireSecond * 1000);
  return timer;
};
