/**
 *  1、发起请求，获得待办理列表、已办理列表、失效列表
    2、查看列表中已办理的履职管理的详情
    3、提交待办理履职管理详情--履职任务的表单
    4、查看已失效的履职管理的详情
 */

import http from "@/utils/data-api-service";
import dayjs from "dayjs";

export function getPerformList(params: any) {
  const { status, startTime, endTime, start, length, personID } = params;
  const apiParam: IShowTableData = {
    tableName: "view_ExecuteMg_Deal",
    length: length,
    start: start,
    type: "TABLE_QUERY",
    params: {
      filterCondition: [
        {
          type: "1",
          field: "view_ExecuteMg_Deal.personID",
          condition: "=",
          connection: "&&",
          id: 1,
          pid: 0,
          value: personID
        },
        {
          type: "1",
          field: "view_ExecuteMg_Deal.status",
          condition: "=",
          connection: "&&",
          id: 1,
          pid: 0,
          value: status
        },
        {
          type: "1",
          field: "view_ExecuteMg_Deal.startTime",
          condition: ">=",
          connection: "&&",
          id: 1,
          pid: 0,
          value: startTime
        },
        {
          type: "1",
          field: "view_ExecuteMg_Deal.startTime",
          condition: "<=",
          connection: "&&",
          id: 1,
          pid: 0,
          value: endTime
        }
      ],
      orderBy: [
        {
          fieldName: "view_ExecuteMg_Deal.startTime",
          sort: "desc"
        }
      ]
    },
    fieldName: [
      "view_ExecuteMg_Deal.id",
      "view_ExecuteMg_Deal.startTime",
      "view_ExecuteMg_Deal.endTime",
      "view_ExecuteMg_Deal.handleTime",
      "view_ExecuteMg_Deal.executeName"
    ]
  };
  return new Promise((resolve, reject) => {
    http
      .request({
        url: `/api/restApi/controlData/showTableData`,
        method: "POST",
        data: apiParam
      })
      .then(res => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
}

export function getPerformDetail(params: any) {
  const { id, status } = params;
  return new Promise((resolve, reject) => {
    http
      .request({
        url: `/api/restApi/xjhx/ljgl/handle/info/${id}/${status}`,
        method: "GET"
      })
      .then(res => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
}

