export function savePerformDetail(params: any) {
  const { id, manuals, autos, remark, handleTime, status } = params;
  const apiParam: IInsertBusinessData = {
    tableName: "ExecuteMg_Deal",
    operation: "UPDATE",
    tableType: 0,
    data: {
      ExecuteMg_Deal: [
        {
          id,
          remark,
          handleTime,
          status
        }
      ],
      ExecuteMg_Deal_Manual: manuals,
      ExecuteMg_Deal_Auto: autos
    }
  };
  return new Promise((resolve, reject) => {
    http
      .request({
        url: `/api/restApi/pageControl/insertBusinessData`,
        method: "POST",
        data: apiParam
      })
      .then(res => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
}

export function getPerformPersonalCount(params: any) {
  const { memberId, endTime } = params;
  let startofMonth = "";
  let endofMonth = "";
  if (dayjs(endTime, "YYYY-MM") > dayjs().subtract(1, "month")) {
    startofMonth = dayjs().startOf("month").format("YYYY-MM-DD");
    endofMonth = dayjs().subtract(1, "day").format("YYYY-MM-DD");
  } else {
    startofMonth = dayjs(endTime).startOf("month").format("YYYY-MM-DD");
    endofMonth = dayjs(endTime).endOf("month").format("YYYY-MM-DD");
  }
  const apiParam: IShowTableData = {
    tableName: "view_member_executetask_detail",
    start: 0,
    length: 10,
    type: "TABLE_QUERY",
    params: {
      orderBy: [],
      filterCondition: [
        {
          type: "1",
          field: "view_member_executetask_detail.endTime",
          condition: ">=",
          connection: "&&",
          id: 1,
          pid: 0,
          value: startofMonth
        },
        {
          type: "1",
          field: "view_member_executetask_detail.endTime",
          condition: "<=",
          connection: "&&",
          id: 2,
          pid: 0,
          value: endofMonth
        },
        {
          type: "1",
          field: "view_member_executetask_detail.memberId",
          condition: "=",
          connection: "&&",
          id: 2,
          pid: 0,
          value: memberId
        }
      ]
    },
    subQuery: [],
    fieldName: [
      "view_member_executetask_detail.complete",
      "view_member_executetask_detail.incomplete",
      "view_member_executetask_detail.nomal",
      "view_member_executetask_detail.abnomal",
      "view_member_executetask_detail.notChecked",
      "view_member_executetask_detail.positionId",
      "view_member_executetask_detail.orgId"
    ],
    statisticsColumn: [
      {
        tableName: "view_member_executetask_detail",
        columnType: "NOMAL",
        columnName: "positionId"
      },
      {
        tableName: "view_member_executetask_detail",
        columnType: "NOMAL",
        columnName: "orgId"
      },
      {
        tableName: "view_member_executetask_detail",
        columnType: "SUM",
        columnName: "complete"
      },
      {
        tableName: "view_member_executetask_detail",
        columnType: "SUM",
        columnName: "incomplete"
      },
      {
        tableName: "view_member_executetask_detail",
        columnType: "SUM",
        columnName: "nomal"
      },
      {
        tableName: "view_member_executetask_detail",
        columnType: "SUM",
        columnName: "abnomal"
      },
      {
        tableName: "view_member_executetask_detail",
        columnType: "SUM",
        columnName: "notChecked"
      },
      {
        tableName: "view_member_executetask_detail",
        columnType: "FORMULA",
        columnName: "completionRate1",
        formula:
          "ROUND(SUM(view_member_executetask_detail.complete)/(CASE (SUM(view_member_executetask_detail.complete)+SUM(view_member_executetask_detail.incomplete)) WHEN 0 THEN 1 ELSE (SUM(view_member_executetask_detail.complete)+SUM(view_member_executetask_detail.incomplete)) END )*100,2)",
        characters: "%",
        formulaColumnsList: ["view_member_executetask_detail.complete", "view_member_executetask_detail.incomplete"]
      }
    ],
    controlPermissionModelDTOs: []
  };

  return new Promise((resolve, reject) => {
    http
      .request({
        url: `/api/restApi/controlData/showTableData`,
        method: "POST",
        data: apiParam
      })
      .then(res => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
}
