import { observable, action, runInAction, computed, observe } from "mobx";

class RemovePersonStore {
  @observable pathname: string = "/choose_person";
  @observable dataSource: Array<any> = [];
  @observable usersMap: Map<string, any> = new Map();
  @computed get pageTitle() {
    return `已选择${this.dataSource.length}人`;
  }

  @computed get users() {
    return this.dataSource.filter((item) => !this.usersMap.has(item.memberId));
  }

  @action initStore = (params: object) => {
    Object.keys(params).forEach((key) => {
      this[key] = params[key];
    });
  };

  @action allCheckBoxHandle = () => {
    this.dataSource.forEach((item) => {
      this.usersMap.set(item.memberId, item);
    });
  };

  @action clearCheckBoxHandle = () => {
    this.usersMap.clear();
  };

  @action onSelectChange = (rowData: any) => {
    if (this.usersMap.has(rowData.memberId)) {
      this.usersMap.delete(rowData.memberId);
    } else {
      this.usersMap.set(rowData.memberId, rowData);
    }
  };
}

export default new RemovePersonStore();
