import http from "@/utils/data-api-service";
// import { getDetailPageData } from '@/service/http/common';
import req from "@/service/http";
import { IFetchMeetingManagement, IFetchMeetingManagementDetail, ISubmitMeetingManagement, IFetchMeetingReserve } from "../interface";
/**
 * 会议管理:获取列表
 * @param params
 * filterCondition: 筛选条件
 * orderBy: 排序
 */
function fetchMeetingManagement({ skip, limit, filterCondition, orderBy }: IFetchMeetingManagement) {
  return req.getShowTableData({
    tableName: "meeting_info_wx_view",
    length: limit,
    start: skip,
    type: "TABLE_QUERY",
    params: {
      filterCondition,
      orderBy: orderBy || [{ fieldName: "meeting_info_wx_view.startTime", sort: "DESC" }]
    },
    subQuery: [],
    fieldName: [
      "meeting_info_wx_view.id",
      "meeting_info_wx_view.name",
      "meeting_info_wx_view.endTime",
      "meeting_info_wx_view.startTime",
      "meeting_info_wx_view.address",
      "meeting_info_wx_view.type",
      "meeting_info_wx_view.workNumber",
      "meeting_info_wx_view.confereeId",
      "meeting_info_wx_view.singinType",
      "meeting_info_wx_view.roomName"
    ],
    controlPermissionModelDTOs: []
  });
}

/**
 * 会议管理:获取详情
 * @param params {id: "会议ID"}
 */
function fetchMeetingManagementDetail(params: IFetchMeetingManagementDetail) {
  return req.getDetailPageData({
    tableInfo: [
      {
        tableName: "meeting_details_info_wx_view",
        tableType: 1,
        dataColumns: [
          { columnName: "avoidLaterTime" },
          { columnName: "cancelstate" },
          { columnName: "specific_address" },
          { columnName: "memberName" },
          { columnName: "now_address" },
          { columnName: "longitude" },
          { columnName: "followId" },
          { columnName: "latitude" },
          { columnName: "workNumber" },
          { columnName: "startTime" },
          { columnName: "endTime" },
          { columnName: "id" },
          { columnName: "name" },
          { columnName: "meetingDescribe" },
          { columnName: "meetingSponsor" },
          { columnName: "confereeId" },
          { columnName: "confereeName" },
          { columnName: "checkinDelay" },
          { columnName: "checkinEarly" },
          { columnName: "scope" },
          { columnName: "roomName" },
          { columnName: "roomId" },
          { columnName: "specificAddress" },
          { columnName: "conferenceTableId" },
          { columnName: "signinType" },
          { columnName: "nowAddress" }
        ],
        filterCondition: [
          {
            field: "id",
            type: 3,
            value: params.id
          }
        ]
      }
    ],
    controlPermissionModelDTOs: []
  });
}

/**
 * 定制接口: 新增/修改会议数据接口
 * @param data 参考 ISubmitMeetingManagement
 */
function submitMeetingManagement(data: any) {
  return new Promise((resolve, reject) => {
    http
      .request({
        url: "/api/restApi/meeting//wx/save",
        method: "POST",
        data
      })
      .then((res: any) => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(new Error(err.message));
      });
  });
}

/**
 * 定制接口: 会议室预订查询
 * @param userId 日期（年-月-日）
 * @param name 用户id
 * @param date 会议室名
 */
function fetchMeetingReserve(data: IFetchMeetingReserve) {
  return new Promise((resolve, reject) => {
    http
      .request({
        url: "/api/restApi/meeting/booking/query",
        method: "POST",
        data
      })
      .then((res: any) => {
        const { result, success } = res;
        if (success && Array.isArray(result)) {
          resolve(result);
        } else {
          reject(new Error("请求出错"));
        }
      })
      .catch((err: Error) => {
        reject(new Error(err.message));
      });
  });
}

/**
 * 取消会议
 * @param id
 */
function submitCancelMeeting(id: string) {
  return new Promise((resolve, reject) => {
    http
      .request({
        url: "/api/restApi/meeting/wx/cancel",
        method: "GET",
        params: { id }
      })
      .then((res: any) => {
        const { result, success } = res;
        if (success) {
          resolve(result);
        } else {
          reject(new Error("请求出错"));
        }
      })
      .catch((err: Error) => {
        reject(new Error(err.message));
      });
  });
}

export { fetchMeetingManagement, fetchMeetingManagementDetail, fetchMeetingReserve, submitMeetingManagement, submitCancelMeeting };
