import http from "./index";
interface IParam {
  userName: string;
  password: string;
  userId: string;
  clientToken: string;
}

export default function (param: IParam): Promise<any> {
  const { clientToken, ...data } = param;
  return new Promise((resolve, reject) => {
    http
      .request({
        url: "/doWeChat/weChat/login/weChatUserBing",
        method: "POST",
        headers: {
          Authorization: `Bearer ${clientToken}`
        },
        data: data
      })
      .then((res: any) => {
        resolve(res);
      })
      .catch((err: any) => {
        reject(err);
      });
  });
}
