/**
 * 去除通用接口前缀
 * @param prefix 前缀
 * @param data 后端返回列表数组
 */
function removePrefix(prefix: string, data: any[]) {
    if (Array.isArray(data)) {
        let tmp: any[] = data.map((item, i) => {
            let keys = Object.keys(item);
            let newItem = {};
            keys.forEach((key) => {
                let newKey = key.replace(prefix + '_', '');
                newItem[newKey] = item[key];
            });
            return newItem;
        });
        return tmp;
    }
    return [];
}

export { removePrefix };
