import http from "./index";
import CryptoJS from "crypto-js";
const encrypt = function (word: any, keyStr: any) {
  var key = CryptoJS.enc.Utf8.parse(keyStr);
  var srcs = CryptoJS.enc.Utf8.parse(word);
  var encrypted = CryptoJS.AES.encrypt(srcs, key, {
    mode: CryptoJS.mode.ECB,
    padding: CryptoJS.pad.Pkcs7
  });
  return encrypted.toString();
};

interface IParam {
  username: string;
  password: string;
  clientToken: string;
  wxCard: string;
  wxUser: string;
  deviceId: string;
}

export default function (param: IParam): Promise<any> {
  const { clientToken, username, password } = param;

  const apiParams = Object.assign(param, {
    username,
    password: encrypt(username + password, "flowhysuseraesco")
  });

  return new Promise((resolve, reject) => {
    http
      .request({
        url: "/doWeChat/weChat/login/userLogin",
        method: "POST",
        headers: { Authorization: "Bearer " + clientToken },
        data: apiParams
      })
      .then((res: any) => {
        resolve(res);
      })
      .catch((err: any) => {
        reject(err);
      });
  });
}
