import Cookie from "js-cookie";
import getSearchParams from "@/utils/tools/getSearchParams";
import storage from "@/utils/storage";
import { Toast } from "antd-mobile";

/**
 * 根据配置过滤接口返回的数据
 */
function filterResBoby(response, httpInstance) {
  const { data, config } = response;
  const { resBodyConfig } = httpInstance || [];
  let res = {};
  resBodyConfig.forEach((item) => {
    if (config.url.indexOf(item.url) >= 0) {
      if (data[item.success.key] === item.success.value) {
        res = { status: true, result: data[item.dataKey] };
      } else {
        res = { status: false, result: data[item.message] };
      }
      return false;
    }
  });
  return res;
}
/**
 * 请求拦截
 */
export function initReqInterceptor(httpInstance) {
  httpInstance.interceptors.request.use(

    (config) => {
      if (config.url.indexOf("auth/webLogin") >= 0) return config;

      const userToken = storage.getItem("userToken");
      const userId = storage.getItem("userId");
      const clinct_token = storage.getItem("client_token");
      if (userId) {
        config.headers.common["Authorization"] = "Bearer " + clinct_token;
        config.headers.common["userId"] = userId;
      } else {
        config.headers.common["Authorization"] = "Bearer " + userToken;
      }
      return config;
    },
    function (error) {
      return Promise.reject(error);
    }
  );
}
/**
 * 响应拦截
 */
export function initResInterceptor(httpInstance) {
  httpInstance.interceptors.response.use(
    function (response) {
      return response.data;
    },
    function (error) {
      console.log("error", error.response, error.message);
      const { status } = error.response || {};
      if (status == 504) {
        error.message = status + ":服务器响应异常";
      } else if (status == 404) {
        error.message = status + ":未找到请求资源";
      } else {
        error.message = error.message || "未正确请求到数据";
      }
      Toast.fail(error.message, 1);

      return Promise.reject(error.message);
    }
  );
}
