import storage from "@/utils/storage";
import Stomp from "stompjs";
import SockJS from "sockjs-client";

class Socket {
  static instance: any;
  retryTimes: number;
  Timer: any;
  client: any;
  unreadMessageCount: number;
  constructor() {
    this.client = null;
    this.Timer = null;
    this.retryTimes = 0;
    this.unreadMessageCount = 0;
  }
  static getInctance() {
    if (!this.instance) {
      // 只能 new 一次
      this.instance = new Socket();
    }
    return this.instance;
  }
  addUnreadMessageCount() {
    this.unreadMessageCount += 1;
    emitter.emit("updateMessageCount", this.unreadMessageCount);
  }
  setUnreadMessageCount(count: number) {
    this.unreadMessageCount = count;
    emitter.emit("updateMessageCount", this.unreadMessageCount);
  }
  addWebSocketParams() {
    const clientToken = storage.getItem("client_token");
    const userId = storage.getItem("userId");
    const userToken = storage.getItem("tokenId");
    const isBinding = storage.getItem("isBinding");
    console.log("isBinding", Number(isBinding));
    if (Number(isBinding) === 1 && clientToken && userId) {
      console.log("用户已绑定", `?token=${clientToken}&user_id=${userId}`);
      return `${window.baseURL || ""}/stomp1/websocket?token=${clientToken}&user_id=${userId}`;
    } else if (Number(isBinding) === 0 && userToken) {
      console.log("用户未绑定", `?token=${userToken}`);
      return `${window.baseURL || ""}/stomp1/websocket?token=${userToken}`;
    }
    return null;
  }
  errorCallBack = (err: any) => {
    console.log("websocket 连接出错", err);
    this.retryTimes += 1;
    let waitTime = 0;
    if (this.retryTimes > 6) {
      waitTime = 64;
    } else {
      waitTime = Math.pow(2, this.retryTimes);
    }
    this.Timer = setTimeout(() => {
      try {
        this.connect();
      } catch (e) {
        console.error(e);
      }
    }, waitTime * 5000);
  };
  connect() {
    const self = this;
    const url = self.addWebSocketParams();

    if (url) {
      let sock = new SockJS(url);
      self.client = Stomp.over(sock);
      self.client.connect(
        {
          "Access-Control-Allow-Origin": "*",
          "Access-Control-Allow-Credentials": true
        },
        function connectCallback(frame: any) {
          console.log("建立连接", frame);
          self.client.subscribe(
            `${window.baseURL || ""}/user/queue/wx-message`,
            (msg: any) => {
              console.log("新通知", msg);
              self.addUnreadMessageCount();
              //setUnreadMessageCount(preUnreadMessageCount => preUnreadMessageCount + 1);
            },
            {
              "Access-Control-Allow-Origin": "*",
              "Access-Control-Allow-Credentials": true
            }
          );
        },
        self.errorCallBack
      );

      self.client.onDisconnect = (err: any) => {
        console.log("SockJsClient onDisconnect: ", err);
      };
    }
  }

  close() {
    if (this.client) {
      this.client.disconnect();
      this.client = null;
    }
  }
}

export default Socket.getInctance();
