import axios from "axios";
import { initReqInterceptor, initResInterceptor } from "./lib/interceptors";
import { addResBodyConfig } from "./lib/addResBodyConfig";
import { webLogin } from "./lib/webLogin";
import { webLogout } from "./lib/webLogout";
import { showTableData } from "./lib/showTableData";
import { getUserInfo } from "./lib/getUserInfo";

const DataApiService = axios.create({ baseURL: window.baseURL || "", timeout: 15000 });

initReqInterceptor(DataApiService);
initResInterceptor(DataApiService);

DataApiService.addResBodyConfig = addResBodyConfig.bind(DataApiService);
DataApiService.webLogin = webLogin.bind(DataApiService);
DataApiService.webLogout = webLogout.bind(DataApiService);
DataApiService.showTableData = showTableData.bind(DataApiService);
DataApiService.getUserInfo = getUserInfo.bind(DataApiService);

export default DataApiService;
