import { observable, action, runInAction, computed, toJS } from "mobx";
import dayjs from "dayjs";
import { Toast } from "antd-mobile";
// import { IPicker, IDetail, IKv } from '../../interface';
import { fetchMeetingReserve } from "@/service/http/meeting/management";
class SelectMeetingRoomStore {
  title: string = "选择会议室";
  dayline: any[] = Array.from({ length: 48 - 14 }, (v, k) => k); // 天横条刻度数组
  hourNumber: any[] = Array.from({ length: 24 - 7 }, (v, k) => k); // 天数字刻度数组
  week = ["日", "一", "二", "三", "四", "五", "六"]; // 周几汉字对应
  @observable isLoading: boolean = false;
  @observable visible: boolean = false; // 选择时间弹窗开启状态
  @observable date: string = dayjs().format(); // 当前选中日期（默认今天）
  @observable data: any[] = []; // 会议室数据
  @observable searchText: string = ""; // 搜索框文字
  @observable curSelectMeeting: any; // 当前选中会议室
  @observable curMeetingInfo: any; // 当前会议参数

  @observable cycle: any = [];

  @computed get criticalPoint() {
    if (dayjs(this.date).format("YYYY-MM-DD") === dayjs().format("YYYY-MM-DD")) {
      return Math.ceil((dayjs().valueOf() - dayjs().startOf("day").valueOf()) / 1800000) - 1;
    } else {
      return 0;
    }
  }

  @action initCurMeeting = (curMeetingInfo: any) => {
    this.curMeetingInfo = curMeetingInfo;
  };
  @action reset = () => {
    this.date = dayjs().format();
    this.searchText = "";
    this.data = [];
    this.curSelectMeeting = null;
    this.curMeetingInfo = null;
    this.visible = false;
    this.isLoading = false;
  };
  @action onCheckboxItemChange = (item: any, i: number, bool: boolean) => {
    this.cycle[i].checked = bool;
  };

  /**
   * 打开弹窗时根据选中会议室刷新数据
   */
  @action getCycle = () => {
    this.cycle = Array.from({ length: 48 - 14 }, (v, k) => k).map((v, i) => {
      let start = dayjs().startOf("day").valueOf() + 7 * 3600000 + i * 1800000;
      let end = dayjs().startOf("day").valueOf() + 7 * 3600000 + (i + 1) * 1800000;
      let startText = dayjs(start).format("HH:mm");
      let endText = dayjs(end).format("HH:mm");
      let isUse = false;
      let isCurMeeting = false;
      let checked = false;
      let name = "";

      for (let i = 0; i < this.curSelectMeeting.bookingTimeSlot.length; i++) {
        let slot = this.curSelectMeeting.bookingTimeSlot[i];
        if (slot.startTime === startText || slot.endTime === endText) {
          isUse = true;
          name = slot.name;
        } else if (dayjs(slot.start).isBefore(dayjs(start)) && dayjs(slot.end).isAfter(dayjs(end))) {
          isUse = true;
          name = slot.name;
        }
        if (isUse) {
          if (this.curMeetingInfo && this.curMeetingInfo.meetingsponsor && this.curMeetingInfo.meetingId) {
            if (slot.meetingsponsor === this.curMeetingInfo.meetingsponsor) {
              name = "你";
              if (slot.meetingId === this.curMeetingInfo.meetingId) {
                isCurMeeting = true;
              }
            }
          }
          break;
        }
      }

      if (this.curMeetingInfo && this.curMeetingInfo.selectMeeting && this.curMeetingInfo.selectMeeting.id) {
        if (
          dayjs(this.curMeetingInfo.selectMeeting.startTime).format("HH:mm") === startText ||
          dayjs(this.curMeetingInfo.selectMeeting.endTime).format("HH:mm") === endText
        ) {
          checked = true;
        } else if (
          dayjs(this.curMeetingInfo.selectMeeting.startTime).isBefore(dayjs(start)) &&
          dayjs(this.curMeetingInfo.selectMeeting.endTime).isAfter(dayjs(end))
        ) {
          checked = true;
        }
      }

      return {
        isUse,
        isCurMeeting,
        name,
        checked,
        label: `${dayjs(start).format("HH:mm")} ~ ${dayjs(end).format("HH:mm")}`,
        start: startText,
        end: endText,
        isExpire: i + 14 < this.criticalPoint ? true : false
      };
    });
  };

  /**
   * 搜索框 onChange 事件
   * @param value
   */
  @action onSearchTextChange = (value: string) => {
    this.searchText = value;
  };

  /**
   * 选择时间弹窗 onChange 事件
   * @param value
   */
  @action onVisibleChange = (value: boolean) => {
    this.visible = value;
  };

  /**
   * 选择会议室 onChange 事件
   * @param value
   */
  @action onSelectMeeting = (item: any) => {
    this.curSelectMeeting = item;
    this.getCycle();
  };

  /**
   * 日期改变
   * @param date
   */
  @action onDateChange = (date: Date) => {
    this.date = dayjs(date).format();
    this.fetchData();
  };

  /**
   * 获取指定日期会议预约数据
   * @param date
   */
  @action fetchData = (date?: string) => {
    if (this.isLoading) return;
    this.isLoading = true;
    let day = dayjs(date || this.date).format("YYYY-MM-DD");
    fetchMeetingReserve({
      day
    }).then((res: any[]) => {
      if (res && res.length > 0) {
        let formatRes = res.map(item => ({
          ...item,
          bookingTimeSlot: Array.isArray(item.bookingTimeSlot)
            ? item.bookingTimeSlot.map((slot: any) => ({
                ...slot,
                start: dayjs(`${day} ${slot.startTime}`).format(),
                end: dayjs(`${day} ${slot.endTime}`).format()
              }))
            : []
        }));

        let data: any[] = formatRes.map((item: any) => {
          let useLine: number[] = [];
          if (item.bookingTimeSlot && item.bookingTimeSlot.length > 0) {
            item.bookingTimeSlot.forEach((useItem: any) => {
              if (useItem.start && useItem.end && dayjs(useItem.start).startOf("day").isSame(dayjs(useItem.end).startOf("day"))) {
                let start = Math.round((dayjs(useItem.start).valueOf() - dayjs(useItem.start).startOf("day").valueOf()) / 1800000);
                let end = Math.round((dayjs(useItem.end).valueOf() - dayjs(useItem.end).startOf("day").valueOf()) / 1800000);
                if (start < end) {
                  while (start < end) {
                    useLine.push(start);
                    start += 1;
                  }
                }
              }
            });
          }
          item.useLine = useLine;
          return item;
        });

        runInAction(() => {
          this.data = data;
          this.isLoading = false;
        });
      }
    });
  };
}

export default new SelectMeetingRoomStore();
