import DataApiService from "../dataApiServiceConfig";

import {
  compositeGetDetailPageData,
  compositeAddDeviceFollow,
  compositeDeleteDeviceFollow,
} from "./compositeParams";
import {
  IAddDeviceFollow,
  IDeleteDeviceFollow,
  IFetchDefenseAreaDetail,
} from "./interface";

export const addDeviceFollow = function (data: IAddDeviceFollow): Promise<any> {
  return new Promise((resolve, reject) => {
    DataApiService.request({
      url: "/api/restApi/pageControl/insertBusinessData",
      method: "POST",
      data: compositeAddDeviceFollow(data),
    })
      .then((res) => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
};

export const deleteDeviceFollow = function (
  data: IDeleteDeviceFollow
): Promise<any> {
  return new Promise((resolve, reject) => {
    DataApiService.request({
      url: "/api/restApi/controlData/deleteTableRecord",
      method: "POST",
      data: compositeDeleteDeviceFollow(data),
    })
      .then((res) => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
};

export const fetchDefenseAreaDetail = function (
  this: any,
  data: IFetchDefenseAreaDetail
): Promise<any> {
  return new Promise((resolve, reject) => {
    DataApiService.request({
      url: "/mock/api/restApi/controlData/getDetailPageData",
      method: "POST",
      data: compositeGetDetailPageData({
        tableName: "view_defense_area_information",
        filterCondition: [
          {
            field: "id",
            type: 3,
            value: data.id,
          },
        ],
        dataColumns: [
          { columnName: "areaLocationName" },
          { columnName: "orgName" },
          { columnName: "groupId" },
          { columnName: "memberName" },
          { columnName: "buildId" },
          { columnName: "floorId" },
          { columnName: "followId" },
          { columnName: "areaId" },
          { columnName: "phone" },
          { columnName: "statusTime" },
          { columnName: "name" },
          { columnName: "id" },
          { columnName: "mainFrameName" },
          { columnName: "memberId" },
          { columnName: "status" },
        ],
      }),
    })
      .then((res) => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
};
