export const setAuthorization = (params: any) => {
  const { userId, client_token, userToken, isBinding } = params;
  if (userId) {
    storage.setItem("userId", userId);
    storage.setItem("client_token", client_token);
  } else {
    storage.setItem("userToken", userToken);
  }
  storage.setItem("isBinding", isBinding);
};

