import DataApiService from "@/utils/data-api-service";
import { IShowTableDataParams } from "./interface";

export default function (param: any): Promise<any> {
  return new Promise((resolve, reject) => {
    DataApiService.request({
      url: "/api/restApi/controlData/showTableData",
      method: "POST",
      data: param
    })
      .then(res => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err.toString());
      });
  });
}
