export default class Emitter {
  all: any;
  constructor() {
    this.all = new Map();
  }
  on(type: string, handler: (arg?: any) => void) {
    const handlers = this.all.get(type);
    const added = handlers && handlers.push(handler);
    if (!added) {
      this.all.set(type, [handler]);
    }
  }

  off(type: string, handler: (arg?: any) => void) {
    const handlers = this.all.get(type);
    if (handlers) {
      handlers.splice(handlers.indexOf(handler) >>> 0, 1);
    }
  }

  emit(type: string, evt: any) {
    (this.all.get(type) ?? []).forEach((handler: (arg?: any) => void) => {
      handler(evt);
    });
    (this.all.get("*") ?? []).forEach((handler: any) => {
      handler(type, evt);
    });
  }

  clear() {
    this.all.clear();
  }
}

export interface EmitterInstance {
  on: (type: string, handler: (arg?: any) => void) => void;
  off: (type: string, handler: (arg?: any) => void) => void;
  emit: (type: string, evt?: any) => void;
  clear: () => void;
}
