// const wx = require("../../core/jweixin-1.2.0");

interface IParams extends WxEvent {
  localId: string;
  isShowProgressTips: number;
}

const API_NAME = "previewImage";

export default {
  previewImage: (param: IParams) => {
    window.wx.previewImage(param);
  },
  getName: (): string => {
    return API_NAME;
  },
};
