/**
 * @description 设置树的模式:单选+叶子节点、单选+所有节点、多选+叶子节点、多选+所有节点
 * @param treeData
 * @param type  可选radio/checkbox
 * @param isLeaf  是否只能选叶子节点
 * @return void
 */
interface ITreeNodeData {
    key: string
    children: ITreeNodeData[]
    [key: string]: any
}
function setTreeMode(treeData: ITreeNodeData[], type: string, isLeaf: boolean) {
    const enumType = {
        "1": type === "radio" && isLeaf,       // 单选+叶子节点
        "2": type === "radio" && !isLeaf,    // 单选+所有节点
        "3": type === "checkbox" && isLeaf,      // 多选+叶子节点
        "4": type === "checkbox" && !isLeaf    // 多选+所有节点
    }
    const enumValue = Object.keys(enumType).filter(item => enumType[item])[0]
    treeData.forEach(item => {
        if (item.children && item.children.length === 0) {
            item.isLeaf = true
            item.checkable = true
        } else {
            item.isLeaf = false
            switch (enumValue) {
                case "1":
                case "3":
                    item.checkable = false
                    break
                case "2":
                case "4":
                    item.checkable = true
                    break
                default:
                    break
            }
            setTreeMode(item.children, type, isLeaf)
        }
    })
}

export {
    setTreeMode 
}