import DataApiService from "@/utils/data-api-service";
export default function (): Promise<any> {
  return new Promise((resolve, reject) => {
    DataApiService.request({
      url: "/mock/api/restApi/Message/getNotice",
      method: "GET",
    })
      .then((res) => {
        resolve(res);
      })
      .catch((err) => {
        reject(err);
      });
  });
}
