import { observable, action, runInAction, computed } from "mobx";
import DataApiService from "@/service/http";
import { removePrefix } from "@/utils/common";

class selectAreaStore {
  @observable curArea: any = {};
  @observable searchText: string = "";
  @observable searchValue: string = "";
  @observable height: number = 0;

  @action reset = () => {
    this.curArea = {};
    this.searchText = "";
    this.searchValue = "";
  };

  @action updateHeight = (height: number) => {
    this.height = height;
  };

  @action updateCurArea = (curArea: any) => {
    this.curArea = curArea;
    this.searchText = "";
    this.searchValue = "";
  };

  @action onSearchValueChange = (value: string) => {
    this.searchValue = value;
  };

  @action updateSearchText = (value: string) => {
    this.searchText = value;
  };

  @action
  fetchVideos = (skip: number, limit: number, params: any) => {
    const filterCondition: object[] = [];
    if (params && params.idKey !== "all" && params.id) {
      filterCondition.push({
        type: "1",
        field: `view_device_location_info.${params.idKey}`,
        condition: "=",
        connection: "&amp;&amp;",
        id: 1,
        pid: 0,
        value: params.id
      });
    }

    if (params.searchText) {
      filterCondition.push({
        type: "1",
        field: `view_device_location_info.channelName`,
        condition: "like",
        connection: "&amp;&amp;",
        id: 1,
        pid: 0,
        value: params.searchText
      });
    }

    return new Promise((resolve, reject) => {
      DataApiService.getShowTableData({
        tableName: "view_device_location_info",
        length: limit,
        start: skip,
        type: "TABLE_QUERY",
        params: Object.assign(
          {},
          filterCondition.length > 0 && {
            filterCondition
          }
        ),
        fieldName: [
          "view_device_location_info.id",
          "view_device_location_info.name",
          "view_device_location_info.status",
          "view_device_location_info.channelId",
          "view_device_location_info.channelName",
          "view_device_location_info.fatherCode"
        ]
      })
        .then((data: any) => {
          const { result, success } = data;
          if (success) {
            resolve({
              data: removePrefix("view_device_location_info", result.aaData).map((item, i) => ({
                ...item,
                number: skip + i + 1,
                id: item.fatherCode,
                realId: item.id
              })),
              total: result.iTotalRecords
            });
          } else {
            reject(new Error("请求设备列表出错"));
          }
        })
        .catch(err => {
          reject(new Error(err));
        });
    });
  };
}

export default new selectAreaStore();
