
const _isSusscess = (status: number|string) => {
    status = status + "";
    return (status.indexOf("2") === 0) || (status.indexOf("3") === 0);

}
export default (res: IHttpResponse) => {
    if(_isSusscess(res.status)){
        return {status: true, data: res.data};
    } else {
        return { status: false, data: res.statusText }
    }
}