import { observable, action, runInAction, computed, values } from "mobx";
import { getPerformDetail, savePerformDetail } from "@/service/http";
import { Toast } from "antd-mobile";
import dayjs from "dayjs";
import { Item } from "@/components/ProtectionList";

type RParam = {
  id: string;
  status: string;
};
class PerformDetailStore {
  history: any = null;
  @observable executeName = ""; //履职任务名称
  @observable startTime = ""; //履职任务日期
  @observable endTime = "";
  @observable handleTime = "";
  @observable orgName = ""; //履职单位
  @observable memberName = ""; //履职人员
  @observable workingBasisName = ""; //工作依据,知识名称
  @observable workingBasisAttas = ""; //知识附件
  @observable remark = "";
  @observable userId = "";
  @observable sumbited = false;
  @observable status = "0";
  @observable autoItems: Array<any> = [];
  @observable manualItems: Array<any> = [];
  @observable reqParams: RParam = {
    id: "",
    status: "0"
  };
  @action
  setSumbited = (statue: boolean) => {
    this.sumbited = statue;
  };
  @action
  clearData = () => {
    this.executeName = "";
    this.startTime = "";
    this.endTime = "";
    this.handleTime = "";
    this.orgName = "";
    this.memberName = "";
    this.workingBasisName = "";
    this.workingBasisAttas = "";
    this.remark = "";
    this.userId = "";
    this.manualItems = [];
    this.autoItems = [];
    this.reqParams = {
      id: "",
      status: "0"
    };
  };
  @action initStore = (params: any) => {
    this.reqParams = params;

    this.performDetailFetch();
  };
  @action
  waitTodoResult = (manualItems: Array<any>) => {
    manualItems.map((Item, index) => {
      if (Item.result) {
        manualItems[index].result = manualItems[index].resultCode;
      }
      if (!Item.result) {
        manualItems[index].result = "0";
      }
    });
  };
  @action onTextareaChange = (val: string) => {
    this.remark = val;
  };
  @action onManualItemsChange = (key: string, value: string, index: number) => {
    this.manualItems[index][key] = value;
  };
  ThroudCheck = (manualItems: Array<any>) => {
    let flag = true;
    const index = 0;
    manualItems.filter((value: any, index) => {
      if (value.result == "1" && !value.remark) {
        flag = false;
      }
    });
    return flag;
  };
  ManualItemsHaveDone = (manualItems: Array<any>) => {
    let flag = true;
    manualItems.forEach(Item => {
      if (!Item.result) {
        flag = false;
        return flag;
      }
    });
    return flag;
  };
  @action onAutoItemsChange = (key: string, value: string, index: number) => {
    this.autoItems[index][key] = value;
  };
  @action onSubmit = (callback: Function) => {
    this.setSumbited(true);
    if (!this.ThroudCheck(this.manualItems)) {
      Toast.info("所有异常项的描述不能为空");
      this.setSumbited(false);
      return;
    }
    // console.log('this.ManualItemsHaveDone',this.ManualItemsHaveDone(this.manualItems))
    if (!this.ManualItemsHaveDone(this.manualItems)) {
      Toast.info("请完成手动填写的内容");
      this.setSumbited(false);
      return;
    }
    this.manualItems.forEach((Item, i) => {
      this.manualItems[i].photo = Item.pics;
      // delete this.manualItems[i].pics
    });
    savePerformDetail({
      id: this.reqParams.id,
      remark: this.remark,
      handleTime: dayjs().format("YYYY-MM-DD HH:mm:ss"),
      status: "1",
      manuals: this.manualItems,
      autos: this.autoItems
    }).then((res: any) => {
      if (res.code === 0) {
        callback();
        this.remark = "";
      }
    });
  };
  @action performDetailFetch = () => {
    getPerformDetail(this.reqParams).then((res: any) => {
      if (res.code === 0) {
        this.setSumbited(false);
        runInAction(() => {
          this.autoItems = res.result.autoItems;
          this.manualItems = res.result.manualItems;
          this.waitTodoResult(this.manualItems);
          this.executeName = res.result.executeName;
          this.memberName = res.result.memberName;
          this.orgName = res.result.orgName;
          this.workingBasisName = res.result.workingBasisName;
          this.workingBasisAttas = res.result.workingBasisAttas;
          this.startTime = res.result.startTime;
          this.endTime = res.result.endTime;
          this.handleTime = res.result.handleTime;
          this.userId = res.result.userId;
          this.remark = this.reqParams.status != "0" ? res.result.remark : "";
          this.status = res.result.status;
        });
      } else if (res.code === 99999) {
        Toast.fail("履职任务不存在");
      } else {
        const index = res.message.lastIndexOf(":");
        const message = res.message.substring(index + 1);
        Toast.fail(message);
      }
    });
  };

  @action updatePics = (state: any) => {
    const { index, pics } = state;
    if (this.manualItems[index].result) {
      this.manualItems[index].resultCode = this.manualItems[index].result;
    }
    this.manualItems[index].pics = pics;
  };
}

export default new PerformDetailStore();
