/**
 * @description 修改treeData指定节点的children字段，使用场景:点击节点异步加载数据
 * @param treeData 
 * @param node 
 * @param children 
 * @return void
 */
interface ITreeNodeData {
    key: string
    children: ITreeNodeData[]
    [key: string]: any
}
function updateTreeNodeChildren(treeData: ITreeNodeData[], node, children: ITreeNodeData[]) {
    treeData.forEach(item => {
        if (node == item.key) {
            item.children = children
            return false
        } else if (item.children && item.children.length > 0) {
            updateTreeNodeChildren(item.children, node, children)
        }
    })
}

export {
    updateTreeNodeChildren,
    ITreeNodeData
}
