import { observable, action } from "mobx";
import {notice} from './interface'
import BaseStore from '@/pages/store';
import DataApiService from '@/service/http';
import {EnumState} from './constance'

class noticeDetailStore{
    @observable notice:any={};
    @action
    inistore=(MessData:any)=>{
        const {wx_message_view_time,wx_message_view_origin_message}=MessData;
        let message=JSON.parse( wx_message_view_origin_message);
        this.notice.date=wx_message_view_time;
        this.notice.content=message.detail;
        this.notice.title=message.name;
        // this.notice.url=message.urlCode?message.urlCode:''
    }
    @action
    clearData=()=>{
        this.notice={};
    }
}
export default new noticeDetailStore();