import http from "@/utils/data-api-service";

export function choosePersonFetch(params: any) {
  return new Promise((resolve, reject) => {
    http
      .request({
        url: `/api/restApi/meeting/wx/participants`,
        method: "POST",
        data: params,
      })
      .then((res) => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
}
