import React, { useEffect, useState } from "react";

// UPDATE 把WxApiContext getWxApi整合成一个服务输出
import WxApiContext, { getWxApi } from "@/service/wx-api";
import AppAuthorized from "./AppAuthorized";

export const AppContainer = (props: GProps): ReactElement => {
  return (
    <AppAuthorized>
      <WxApiContext.Provider
        value={{
          wxApi: getWxApi
        }}
      >
        {props.children}
      </WxApiContext.Provider>
    </AppAuthorized>
  );
};
