import { observable, action, runInAction, computed, observe } from "mobx";

class ChooseAddressStore {
  @observable height: number = 0;
  @observable coord: string = "";
  @observable pathname: string = "";
  @action setHeight = (height: number) => {
    this.height = height;
  };
  @action setCoord = (coord: string) => {
    this.coord = coord;
  };
  @action initStore = (params: object) => {
    Object.keys(params).forEach((key) => {
      this[key] = params[key];
    });
  };
}

export default new ChooseAddressStore();
