import { observable, action, toJS, runInAction, computed } from "mobx";
import { Message } from "./interface";
import BaseStore from "@/pages/store";
import { getAllMessage } from "@/service/http";
import storage from "@/utils/storage";

class MessagesStore {
  tokenContext: IToken;
  message: any[] = [];
  pageIndex = 0;
  total = 0;
  length = 20;
  @observable isLoading = false;
  @observable hasMore = true;
  @observable response: any = {};
  @observable TotalNum = 0;
  @observable selected = false;
  @computed get pageNumber() {
    return (this.total - 1) / this.length + 1;
  }
  @computed get userId() {
    return storage.getItem("userId");
  }
  @action.bound
  setSelected(bool: boolean) {
    this.selected = bool;
  }

  @action.bound
  setIsLoading(bool: boolean) {
    this.isLoading = bool;
  }
  pageIndexrest = () => {
    this.pageIndex = 0;
    this.message = [];
    this.setIsLoading(false);
    this.hasMore = true;
  };

  @action onEndReached = () => {
    if (!this.isLoading && this.hasMore) {
      this.getAllmessage();
    }
  };

  @action onRefresh = () => {
    this.pageIndexrest();
    this.getAllmessage();
  };

  @action
  getAllmessage = (callback?: Function) => {
    this.pageIndex += 1;
    const start = (this.pageIndex - 1) * 20;
    this.isLoading = true;

    getAllMessage({ start: start, userId: this.userId })
      .then((res: any) => {
        runInAction(() => {
          this.total = res.result.iTotalRecords;
          this.message = this.message.concat(res.result.aaData);
          if (this.pageIndex > this.pageNumber) {
            this.hasMore = false;
          }
          this.isLoading = false;
          callback && callback();
        });
      })
      .catch(err => {
        console.log(err);
      })
      .finally(() => {
        this.isLoading = false;
      });
  };
  @action
  changeReadStatu = (id: string) => {
    this.message.forEach((item: any, index: number) => {
      if (item.wx_message_view_id == id) {
        this.message[index].wx_message_view_read_status = 1;
      }
    });
  };
}

export default new MessagesStore();
