interface IParams extends WxEvent {
  localId: string;
  isShowProgressTips: number;
}

const API_NAME = "previewFile";

export default {
  previewFile: (param: IParams) => {
    window.wx.previewFile(param);
  },
  getName: (): string => {
    return API_NAME;
  },
};
