import { observable, action, runInAction, computed, toJS } from "mobx";
import { IPicker, IDetail, IKv, ILocation, IMembers } from "../../interface";
import { fetchMeetingManagementDetail, submitMeetingManagement, submitCancelMeeting } from "@/service/http/meeting/management";
import dayjs from "dayjs";
import storage from "@/utils/storage";
import { Toast } from "antd-mobile";
import { getUserInfo } from "@/service/http";

const toast = {
  inside: {
    name: "请输入会议名称",
    meetingDescribe: "请输入会议描述",
    startTime: "请选择开始时间",
    endTime: "请选择结束时间",
    // selectMembers: '请选择参与人',
    // scope: '请选择签到范围',
    checkinEarly: "请选择签到有效开始时间",
    checkinDelay: "请选择签到有效结束时间",
    avoidLaterTime: "请选择迟到缓冲时间"
  },
  outside: {
    name: "请输入会议名称",
    meetingDescribe: "请输入会议描述",
    startTime: "请选择开始时间",
    endTime: "请选择结束时间",
    nowAddress: "请选择会议地点",
    specificAddress: "请选择会议详细地址",
    // selectMembers: '请选择参与人',
    scope: "请选择签到范围",
    checkinEarly: "请选择签到有效开始时间",
    checkinDelay: "请选择签到有效结束时间",
    avoidLaterTime: "请选择迟到缓冲时间"
  }
};

class meetingManagementDetailStore {
  //   @observable isRoute:boolean = false
  title: string = "会议详情";
  locationRange: IPicker[] = [
    // { label: "10m", value: 10 },
    // { label: "20m", value: 20 },
    { label: "50m", value: 50 },
    { label: "100m", value: 100 },
    { label: "200m", value: 200 },
    { label: "500m", value: 500 }
  ];
  locationRange_kv: IKv = this.locationRange.reduce((a, b) => {
    a[b.value] = b.label;
    return a;
  }, {});
  timeRange: IPicker[] = [
    { label: "0分钟", value: 1 },
    { label: "5分钟", value: 5 },
    { label: "10分钟", value: 10 },
    { label: "20分钟", value: 20 },
    { label: "30分钟", value: 30 },
    { label: "60分钟", value: 60 }
  ];
  timeRange_kv: IKv = this.timeRange.reduce((a, b) => {
    a[b.value] = b.label;
    return a;
  }, {});
  // @observable userInfo: any = null;
  @observable onlyRead: boolean = false;
  @observable pageType: string = "";
  @observable memberName: string = "";
  @observable workNumber: string = "";
  @observable hrNumberId: string = "";
  @observable submiting: boolean = false;
  // @observable isUpdate: boolean = false;
  @observable detail: IDetail = {
    checkinDelay: 1,
    checkinEarly: 30,
    avoidLaterTime: 1,
    selectMembers: []
  };
  @action setPageType = (type: string) => {
    this.pageType = type;
  };
  @action reset = () => {
    this.pageType = "";
    this.memberName = "";
    this.workNumber = "";
    this.hrNumberId = "";
    this.onlyRead = false;
    this.submiting = false;
    this.detail = { checkinDelay: 1, checkinEarly: 30, avoidLaterTime: 1, selectMembers: [] };
  };
  @action updateDetail = (detail: IDetail) => {
    this.detail = detail;
    this.updatePersonStr();
    this.updateOnlyRead();
  };
  @action onDetailChange = (field: string, value: string | number) => {
    if (field) {
      this.detail[field] = value;
    }
  };
  @action fetchDetail = (id: string) => {
    fetchMeetingManagementDetail({ id }).then((res: any) => {
      const { result, success } = res;
      if (success && result && result.dataJson && result.dataJson.meeting_details_info_wx_view) {
        runInAction(() => {
          let data = result.dataJson.meeting_details_info_wx_view[0];
          let selectMembers = this.formatMembers(data.confereeId, data.confereeName, data.workNumber, data.signinType, data.conferenceTableId);
          // avoidLaterTime	decimal	迟到缓冲时间设置
          // checkinDelay	Integer	会议签到有效结束时间设置
          // checkinEarly	Integer	会议签到有效开始时间设置
          // endTime	String	结束时间
          // id	varchar	会议主键
          // meetingDescribe	String	描述
          // meetingSponsorId	String	发起人id
          // meetingSponsor	String	发起人
          // name	String	会议名称
          // roomId	String	会议室关联id
          // startTime	String	开始时间
          // total	String	参会人员总数
          // confereeId	String	参会人员id，多数据以逗号拼接如（1，2，3）
          // confereeName	String	参会人员，多数据以逗号拼接如（张三，李四，王五）
          // workNumber	string	工号，多数据以逗号拼接如（001，002，003）
          // signinType	string	签到类型，多数据以逗号拼接如（0，1，0)0:未签到，1：签到
          // latitude	String	纬度
          // longitude	String	经度
          // roomName	String	会议室名称
          // nowAddress	String	当前坐标地址
          // remarks	String	会议室备注
          // roomCapacity	String	容纳人数
          // scope	Integer	会议室考勤范围
          // specificAddress	String	具体地址
          // type	String	会议室类型
          this.detail = {
            ...data,
            scope: Number(data.scope),
            avoidLaterTime: Number(data.avoidLaterTime) || 1,
            checkinDelay: Number(data.checkinDelay) || 1,
            checkinEarly: Number(data.checkinEarly) || 1,
            scopeBackUp: data.scope,
            // specificAddressBackUp: data.specificAddress,
            selectMembers,
            selectMembersBackUp: selectMembers
          };
          this.updatePersonStr();
          this.updateOnlyRead();
        });
      }
    });
  };

  @action updateLoaction = (location: any) => {
    this.detail = {
      ...this.detail,
      nowAddress: location.poiaddress,
      specificAddress: location.poiname,
      longitude: location.latlng.lng,
      latitude: location.latlng.lat,
      location: location
    };
  };

  @action updateOnlyRead = () => {
    if (this.pageType === "add") {
      this.onlyRead = false;
    } else {
      const { startTime, endTime } = this.detail;
      if (startTime && endTime) {
        let now = dayjs().valueOf();
        let startHour = dayjs().startOf("hour").valueOf();
        let time = now - startHour < 1800000 ? dayjs(startHour).toDate() : dayjs(startHour + 1800000).toDate();
        // let minDate = now - startHour < 1800000 ? dayjs(startHour).toDate() : dayjs(startHour + 1800000).toDate();

        if (dayjs(startTime).valueOf() > dayjs().valueOf()) {
          this.onlyRead = false;
        } else {
          this.updatePersonStr();
          this.onlyRead = true;
        }
      } else {
        this.onlyRead = true;
      }
    }
  };

  @action updatePerson = (selectMembers: any) => {
    this.detail = {
      ...this.detail,
      personStr: "", // 生成人员显示字符串
      selectMembers: selectMembers.map((item: any) => ({
        confereeId: item.memberId,
        confereeName: item.name,
        workNumber: item.workNumber,
        personOrgId: item.orgId
      }))
    };
    this.updatePersonStr();
  };

  @action updateMeeting = (selectMeeting: any) => {
    this.detail = {
      ...this.detail,
      roomId: selectMeeting.id,
      roomName: selectMeeting.name,
      nowAddress: selectMeeting.name,
      // specificAddress: selectMeeting.remarks,
      startTime: selectMeeting.startTime,
      endTime: selectMeeting.endTime,
      selectMeeting
    };
  };

  @action updatePersonStr = () => {
    const { startTime, selectMembers } = this.detail;
    let personStr: any;
    if (this.pageType === "edit" && dayjs(startTime).isBefore(dayjs())) {
      let signIn_num: number = selectMembers.filter(item => Number(item.signType) === 1).length;
      personStr = `参与人：${selectMembers.length - signIn_num}人未签到，${signIn_num}人已签到`;
    } else {
      let person_name: string = selectMembers
        .filter((item, i) => i < 3)
        .map(item => item.confereeName)
        .join(",");
      personStr = selectMembers.length > 0 ? `参与人：${person_name}等${selectMembers.length}人` : `参与人：无`;
    }
    this.detail.personStr = personStr;
  };

  @action submit = (userInfo: any, params: any, cb: Function) => {
    if (this.submiting) return;
    const { type, id } = params || {};
    const { workNumber, hrNumberId, orgId } = userInfo || {};
    if (!type || !id || !hrNumberId || !orgId) {
      return;
    }
    let toastKeys = Object.keys(toast[type]);
    let hasError = false;
    for (let i = 0; i < toastKeys.length; i++) {
      if (!this.detail[toastKeys[i]]) {
        Toast.info(toast[type][toastKeys[i]], 1);
        hasError = true;
        break;
      }
    }
    if (hasError) return;
    const {
      name,
      roomId,
      avoidLaterTime,
      checkinDelay,
      checkinEarly,
      meetingDescribe,
      endTime,
      startTime,
      scopeBackUp,
      scope,
      specificAddress,
      longitude,
      latitude,
      nowAddress,
      selectMembers
    } = this.detail;

    if (
      !dayjs(startTime).startOf("day").isSame(dayjs(endTime).startOf("day")) &&
      !dayjs(startTime).add(1, "day").startOf("day").isSame(dayjs(endTime))
    ) {
      Toast.info("开始时间和结束时间需在同一天");
      return;
    }

    if (dayjs(startTime).isAfter(dayjs(endTime))) {
      Toast.info("开始时间需小于结束时间");
      return;
    }

    if (dayjs(endTime).valueOf() - dayjs(startTime).valueOf() < Number(checkinDelay) * 60 * 1000) {
      Toast.info("会议时间不可小于签到结束时间", 1);
      return;
    }
    if (Number(avoidLaterTime) > Number(checkinDelay)) {
      Toast.info("缓冲时间不可大于签到结束有效时间", 1);
      return;
    }
    if (Number(checkinEarly) === 1 && Number(checkinDelay) === 1) {
      Toast.info("签到开始和结束有效时间不能同时为0", 1);
      return;
    }
    if (!selectMembers || selectMembers.length <= 0) {
      Toast.info("请选择参与人", 1);
      return;
    }
    this.submiting = true;
    let meeting_appointment: any = {
      AvoidLaterTime: Number(avoidLaterTime) === 1 ? 0 : Number(avoidLaterTime), //Integer	迟到缓冲时间设置
      CheckinDelay: Number(checkinDelay) === 1 ? 0 : Number(checkinDelay), //Integer	Integer	会议签到有效结束时间设置
      CheckinEarly: Number(checkinEarly) === 1 ? 0 : Number(checkinEarly), //Integer	Integer	会议签到有效开始时间设置
      EndTime: dayjs(endTime).format("YYYY-MM-DD HH:mm:ss"), //Integer	datetime	结束时间
      // id, //Integer	String	主键
      meetingDescribe, //Integer	String	描述
      MeetingSponsor: hrNumberId, //Integer	String	发起人
      meeting_sp_org: orgId, //Integer	String	发起人所属机构
      Name: name, //Integer	String	会议名称
      // RoomId: roomId, //Integer	String	会议室关联id
      StartTime: dayjs(startTime).format("YYYY-MM-DD HH:mm:ss")
      // operation: 'INSERT',
    };
    if (type === "inside" && roomId) {
      meeting_appointment.RoomId = roomId;
      let meeting_room: any = {
        id: roomId,
        type: 0
      };
      if (id === "add") {
        // 自有新建
        let selectMembers = this.detail.selectMembers
          // .filter(item => item.workNumber !== workNumber)
          .map(item => ({
            ConfereeId: item.confereeId,
            WorkNumber: item.workNumber,
            personOrgId: item.personOrgId,
            _operation: "INSERT"
          }));
        meeting_appointment.operation = "INSERT";
        meeting_room.scope = scope;
        this.submitOne(meeting_room, "update").then(() => {
          this.submitTwo(meeting_appointment, selectMembers, false).then(res => {
            Toast.success("新建成功", 1);
            cb && cb();
          });
        });
      } else {
        // 自有编辑，需要对比前后人员差异
        let submitSelectMembers = this.getSubmitSelectMembers(roomId, workNumber);
        if (Number(scopeBackUp) !== Number(scope)) {
          meeting_room = {
            ...meeting_room,
            scope
          };
        }
        if (this.detail.id) {
          meeting_appointment.id = this.detail.id;
          meeting_appointment.operation = "UPDATE";
        }
        meeting_appointment.operation = "INSERT";
        this.submitOne(meeting_room, "update").then(() => {
          this.submitTwo(meeting_appointment, submitSelectMembers, true).then(res => {
            Toast.success("编辑成功", 1);
            cb && cb();
          });
        });
      }
    } else if (type === "outside") {
      let meeting_room: any = {
        latitude,
        longitude,
        // Name: name,
        now_address: nowAddress,
        // remarks
        scope,
        specific_address: specificAddress,
        type: 1
      };
      if (id === "add") {
        // 非自有新建
        let selectMembers = this.detail.selectMembers
          // .filter(item => item.workNumber !== workNumber)
          .map(item => ({
            ConfereeId: item.confereeId,
            WorkNumber: item.workNumber,
            personOrgId: item.personOrgId,
            _operation: "INSERT"
          }));
        this.submitOne(meeting_room, "insert").then((res_one: any) => {
          meeting_appointment.RoomId = res_one.result;
          meeting_appointment.operation = "INSERT";
          this.submitTwo(meeting_appointment, selectMembers, false).then(res_two => {
            Toast.success("新建成功", 1);
            cb && cb();
          });
        });
      } else {
        // 非自有编辑，需要对比前后人员差异
        let submitSelectMembers = this.getSubmitSelectMembers(roomId, workNumber);
        // return;
        meeting_room.id = roomId;
        this.submitOne(meeting_room, "update").then((res_one: any) => {
          meeting_appointment.id = this.detail.id;
          meeting_appointment.RoomId = roomId;
          meeting_appointment.operation = "UPDATE";
          this.submitTwo(meeting_appointment, submitSelectMembers, true).then(res_two => {
            Toast.success("编辑成功", 1);
            cb && cb();
          });
        });
      }
    }
  };

  @action cancelMeeting = (id: string, cb: Function) => {
    submitCancelMeeting(id).then(() => {
      Toast.info("取消成功", 1);
      cb && cb();
    });
  };

  submitOne = (meeting_room: any, operation: string) => {
    return submitMeetingManagement({
      tableType: "0",
      tableName: "meeting_room",
      operation,
      data: {
        meeting_room: [meeting_room]
      },
      isCopy: 0,
      nodeType: 0
    }).finally(() => {
      runInAction(() => {
        this.submiting = false;
      });
    });
  };

  submitTwo = (meeting_appointment: any, selectMembers: any, isUpdate: boolean) => {
    return submitMeetingManagement({
      tableType: "0",
      tableName: "meeting_appointment",
      operation: isUpdate ? "update" : "insert",
      data: {
        meeting_appointment: [meeting_appointment],
        meeting_appointment_conferee: selectMembers
      },
      isCopy: 0,
      nodeType: 0
    }).finally(() => {
      runInAction(() => {
        this.submiting = false;
      });
    });
  };

  formatMembers = (confereeId: string, confereeName: string, workNumber: string, signType: string, conferenceTableId: string): IMembers[] => {
    if (!confereeId || !confereeName || !workNumber) return [];
    let arr_confereeId = confereeId.split(",");
    let arr_confereeName = confereeName.split(",");
    let arr_workNumber = workNumber.split(",");
    let arr_signType = signType ? signType.split(",") : [];
    let arr_conferenceTableId = conferenceTableId ? conferenceTableId.split(",") : [];
    if (arr_confereeId.length === arr_confereeName.length && arr_workNumber.length === arr_confereeName.length) {
      return arr_confereeId.map((item: any, i: number) => ({
        confereeId: item,
        confereeName: arr_confereeName[i],
        workNumber: arr_workNumber[i],
        signType: arr_signType[i] || "0",
        id: arr_conferenceTableId[i] || ""
      }));
    }
    return [];
  };

  getSubmitSelectMembers = (roomId: string, workNumber: string) => {
    let selectMembersBackUp: any = toJS(this.detail.selectMembersBackUp, {
      recurseEverything: true
    });
    let selectMembers = toJS(this.detail.selectMembers, {
      recurseEverything: true
    });
    // .filter(item => item.workNumber !== workNumber);
    let submitSelectMembers: any[] = [];
    let preSelectMemberIds = selectMembersBackUp.map((item: any) => item.confereeId);
    while (selectMembers.length) {
      let selectMember = selectMembers.shift();
      let i = preSelectMemberIds.indexOf(selectMember.confereeId);
      if (i !== -1) {
        submitSelectMembers.push({
          id: selectMembersBackUp[i].id,
          fid: roomId,
          ConfereeId: selectMember.confereeId,
          WorkNumber: selectMember.workNumber,
          // personOrgId: selectMember.personOrgId,
          _operation: "UPDATE"
        });
        selectMembersBackUp[i].isUpdate = true;
      } else {
        submitSelectMembers.push({
          ConfereeId: selectMember.confereeId,
          WorkNumber: selectMember.workNumber,
          personOrgId: selectMember.personOrgId,
          _operation: "INSERT"
        });
      }
    }
    selectMembersBackUp.forEach((item: any) => {
      if (!item.isUpdate) {
        submitSelectMembers.push({
          id: item.id,
          fid: roomId,
          ConfereeId: item.confereeId,
          WorkNumber: item.workNumber,
          // personOrgId: item.personOrgId,
          _operation: "DELETE"
        });
      }
    });
    return submitSelectMembers;
  };
}

export default new meetingManagementDetailStore();
