import wechatAxiosInstance, {
    getJsapiTicket,
} from '@/service/wechatAxiosInstance';

type TValid = Error | void;

const isValid = (response: IHttpResponse): TValid => {
    const { data } = response;
    const { code } = data;
    if (code !== 0) {
        throw new Error('请求失败');
    }
};

const getSignatureFromTicketResponse = (
    response: IHttpTicketResponse
): string => {
    const { result = {} } = response.data;
    return JSON.parse(result).ticket;
};

async function getFromServer(): Promise<any> {
    try {
        // update 后端会封装前端的access_token和signature的两个请求
        // const accessTokenResponse = await DataApiService.getAccessToken();
        // isValid(accessTokenResponse);
        // const {  } = accessTokenResponse.data;
        const ticketResponse = await getJsapiTicket();
        isValid(ticketResponse);
        const signature = getSignatureFromTicketResponse(ticketResponse);
        return signature;
    } catch (err) {
        // TODO 增加异常处理
        console.log('err', err);
    }
}

const httpSignatureFactory = {
    getSignature: (): Promise<any> => {
        return getFromServer();
    },
};

export default httpSignatureFactory;
