import axios from "axios";
import storage from "@/utils/storage";

/**
 *  1、发起登录请求 （页面->后台->企业微信服务器:获取微信 token 和 wxCard）
    2、系统账户登录 （没有绑定帐号跳转到系统用户绑定界面需要登录系统用户）
    3、绑定微信 （将系统用户与微信账户绑定）
    4、解绑微信（将系统用户与微信账户解绑）
 */
const http = axios.create({ baseURL: window.baseURL || "" });

http.interceptors.request.use(
  function (config) {
    // 在发送请求之前做些什么
    // config.headers.common["Authorization"] ="Bearer " + storage.getItem("clientToken");
    return config;
  },
  function (error) {
    // 对请求错误做些什么
    return Promise.reject(error);
  }
);

// 添加响应拦截器
http.interceptors.response.use(
  function (response) {
    // 对响应数据做点什么
    return response.data;
  },
  function (error) {
    // 对响应错误做点什么
    return Promise.reject(error);
  }
);

export default http;
