/**
 * 退出
 */
import Cookie from "js-cookie";

export function webLogout() {
  return new Promise((resolve, reject) => {
    this.get("/api/restApi/auth/webLoginOut")
      .then((res) => {
        //Cookie.remove("tokenId")
        //Cookie.remove("tokenTime")
        resolve(res);
      })
      .catch((error) => {
        reject(error);
      });
  });
}
