import wechatAxiosInstance from "../index";
const wxData = require("@/resource/wx.json");
const { getWeChatConfig } = require("@/service/http");
interface IParam {
  corpid: string;
  corpsecret: string;
}
/**
 * "errcode":0,
    "errmsg":"ok",
    "access_token":"accesstoken000001",
    "expires_in":7200
 */
interface IResponse extends INormalResponse {
  access_token: string;
}

const CORPID = wxData["corpid"]["value"];
const CORSECRET = wxData["corpsecret"]["value"];

export default function (): Promise<any> {
  return new Promise((resolve, reject) => {
    getWeChatConfig()
      .then((res: any) => {
        const { result } = res;
        wechatAxiosInstance
          .request({
            url: "/cgi-bin/gettoken", //https://qyapi.weixin.qq.com/cgi-bin/gettoken?corpid=ID&corpsecret=SECRECT
            method: "GET",
            data: { corpid: result.corp_id, corpsecret: result.secret }
          })
          .then((res: any) => {
            resolve(res);
          })
          .catch((err: Error) => {
            reject(err);
          });
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
}
