/**
 * @description 在访问企业微信应用的时候，会进行微信的acesstoken和访问用户的微信信息以及获取和应用的鉴权认证
 * 1. 判断用户的微信信息尚未绑定，则跳转登陆页面
 * 2. 判断用户的微信信息已经绑定，则跳转到首页
 */
import React, { useState, useEffect } from "react";
import { setAuthorization } from "@/service/http";
import { setAuthorization as wxSetAuthorization, getWeChatCache } from "@/service/wechatAxiosInstance";
import getSearchParams from "@/utils/tools/getSearchParams";
import storage from "@/utils/storage";
import { expireListener } from "./service";
import { Toast } from "antd-mobile";
import socket from "./websocket";
const auth = (token: IToken): void => {
  const { client_token } = token;
  setAuthorization(token);
  wxSetAuthorization(client_token);
};

interface ICacheExpire {
  client_token: string | number;
}
type SearchMap = {
  wxUser?: string;
  wxCard?: string;
  userId?: string;
  clientToken?: string;
  userToken?: string;
  username?: string;
  access_token?: string;
  memberId?: string;
  isBinding?: string;
};

const updateStorage = (searchMap: SearchMap) => {
  const { wxUser, wxCard, userId, clientToken, userToken, username, access_token, memberId, isBinding } = searchMap;
  storage.setItem("access_token", access_token);
  storage.setItem("wxUser", wxUser);
  storage.setItem("wxCard", wxCard);
  storage.setItem("userId", userId);
  storage.setItem("isBinding", isBinding);
  storage.setItem("client_token", clientToken);
  storage.setItem("tokenId", userToken);
  storage.setItem("userToken", userToken);
  storage.setItem("username", username);
  storage.setItem("memberId", memberId);
};

const getToken = (): IToken => {
  const searchMap = getSearchParams(location.search);
  if (searchMap.clientToken) {
    const { wxUser, wxCard, userId, clientToken, userToken, memberId } = searchMap;
    if (memberId == 0) {
      Toast.info("当前用户未绑定人员信息");
    }
    updateStorage(searchMap);
    return {
      client_token: clientToken,
      wxUser,
      wxCard,
      userId,
      userToken,
      userName: ""
    };
  }
  return {
    client_token: storage.getItem("client_token"),
    wxUser: storage.getItem("wxUser"),
    wxCard: storage.getItem("wxCard"),
    userId: storage.getItem("userId"),
    userToken: storage.getItem("tokenId"),
    userName: storage.getItem("userName")
  };
};

const checkLogin = (token: IToken): boolean => {
  let isLogined = false;
  if (token.client_token && token.userId && token.wxCard && token.wxUser) {
    isLogined = true;
  } else if (token.userToken) {
    isLogined = true;
  }
  return isLogined;
};

const contrastToCache = (wechatCache: IWechatCache, token: IToken): IToken => {
  const newWechatCache = {
    access_token: "",
    client_token: ""
  };
  // 比对返回的token与缓存token的一致性
  Object.keys(newWechatCache).forEach((item: string) => {
    if (newWechatCache[item] !== wechatCache[item]) {
      newWechatCache[item] = wechatCache[item];
    }
    return undefined;
  });
  const newToken = Object.assign({}, token, newWechatCache);
  return newToken;
};

function registerExpireListenerWrapper(setTokenState: (token: IToken) => void, token: IToken) {
  return function registerExpireListener(expireMap: ICacheExpire) {
    Object.keys(expireMap).forEach((item: string) => {
      expireListener(expireMap[item], () => {
        const key = item;
        getWeChatCache().then((wechatCache: IWechatCache) => {
          const newValue = wechatCache[key];
          const newToken = Object.assign({}, token, { key: newValue });
          setStorageTokens(wechatCache);
          setTokenState(newToken);
        });
      });
    });
  };
}
const tokenarrs = ["access_token", "client_token", "we_chat_ticket"];
const setStorageTokens = (tokens: IWechatCache) => {
  tokenarrs.forEach(key => {
    storage.setItem(key, tokens[key]);
  });
};

async function updateToken(): Promise<IWechatCache> {
  try {
    const wechatCache: IWechatCache = await getWeChatCache();
    setStorageTokens(wechatCache);
    return wechatCache;
  } catch (err) {
    console.log(err);
    //Toast.fail(err.toString());
    throw err;
  }
}

async function main(setTokenState: (token: IToken) => void, token: IToken) {
  try {
    if (location.pathname.indexOf("login") > -1) {
      //等登陆时不需要判断是否登陆
    } else {
      const isLogined = checkLogin(token);
      if (isLogined) {
        socket.connect();
      }

      if (!isLogined) throw new Error("您尚未登陆");
      const wechatCache = await updateToken();
      const expireMap = {
        client_token: wechatCache.client_token_expires_in
      };
      const newCacheToken = contrastToCache(wechatCache, token);
      const registerExpireListener = registerExpireListenerWrapper(setTokenState, newCacheToken);
      registerExpireListener(expireMap);
    }
  } catch (err) {
    console.error(err);

    throw err;
  }
}

function createUpdateItemInToken(setTokenState: (token: IToken) => void, token: IToken) {
  return function updateItemInToken(item: string, value: string) {
    const newToken = Object.assign({}, token, {
      [item]: value
    });
    setTokenState(newToken);
  };
}

// 1. 首次登陆应用时，要从URL的search或者storage中获取初始化的token值。
const defaultToken = getToken();
let updateItemInToken: (key: string, value: string) => void;
// 2. 然后根据初始化token和clientToken更新方法来构造context组件。
export const TokenContext = React.createContext({
  ...defaultToken,
  updateItemInToken
});

const AppAuthorized = (props: GProps): ReactElement => {
  const [tokenState, setTokenState] = useState(defaultToken);

  auth(tokenState);

  // 3. 更新token以及设置token刷新处理
  useEffect(() => {
    main(setTokenState, tokenState);
    return () => {
      socket.close();
    };
  }, []);

  const updateItemInToken: (key: string, value: string) => void = createUpdateItemInToken(setTokenState, tokenState);

  return <TokenContext.Provider value={{ ...tokenState, updateItemInToken }}>{props.children}</TokenContext.Provider>;
};

export default AppAuthorized;
