import getLocation from "./getLocation";
import locationChangeListenerModule from "./locationChangeListener";
import openLocation from "./openLocation";
import startAutoLBS from "./startAutoLBS";
import stopAutoLBS from "./stopAutoLBS";
import getApiList from "../../tools/getApiList";

const modules = [getLocation, openLocation, locationChangeListenerModule, startAutoLBS, stopAutoLBS];
const apiList = getApiList(modules);

const lcm = locationChangeListenerModule;

export default {
    getLocation,
    locationChangeListener: lcm.locationChangeListener,
    openLocation,
    startAutoLBS,
    stopAutoLBS,
    onLocationChange: lcm.onLocationChange,
    apiList
}
