// const wx = require('../../core/jweixin-1.2.0');

interface IParams extends WxEvent {
    localId: string;
    isShowProgressTips: number;
}

const API_NAME = 'uploadImage';

export default {
    uploadImage: (param: IParams) => {
        window.wx.uploadImage(param);
    },
    getName: (): string => {
        return API_NAME;
    },
};
