// const wx = require('../../core/jweixin-1.2.0');

interface IParams extends WxEvent {
    localId: string;
}

const API_NAME = 'getLocalImgData';

export default {
    getLocalImgData: (params: IParams) => {
        window.wx.getLocalImgData(params);
    },
    getName: (): string => {
        return API_NAME;
    },
};
