import { observable, action, runInAction, computed, observe } from "mobx";
import { choosePersonFetch } from "@/service/http";
import { IProps, IState, IUser, IOrg } from "./type";

class ChoosePersonStore {
  @observable pageTitle: string = "选择参与人";
  @observable pathname: string = "/choose_person";
  @observable searchText: string = "";
  @observable searchValue: string = "";
  @observable usersMap: Map<string, IUser> = new Map(); //选中人员Map
  @observable isRadio: boolean = false;

  @observable departments: Array<IOrg> = []; //部门列表

  @observable persons: Array<IUser> = []; //人员列表

  @observable breadcrumbs: Array<IOrg> = [{ orgId: 0, orgName: "全部", total: 0 }]; //面包屑

  @computed get selected() {
    return Array.from(this.usersMap.values());
  }
  @computed get selectedTitle() {
    let title;
    if (this.selected.length > 2) {
      title = `${this.selected
        .slice(0, 3)
        .map((item: IUser) => item.name)
        .join(",")}等${this.selected.length}人`;
    } else {
      title = `${this.selected.map((item: IUser) => item.name).join(",")}`;
    }
    return title;
  }
  @action reset = () => {
    this.searchText = "";
    this.usersMap = new Map();
    this.isRadio = false;
    this.departments = [];
    this.persons = [];
    this.breadcrumbs = [{ orgId: 0, orgName: "全部", total: 0 }];
  };
  @action setSearchValue = (value: string) => {
    this.searchValue = value;
  };
  @action setSearchText = (value: string) => {
    this.searchText = value;
  };
  @action allCheckBox = (e: any) => {
    const persons = this.persons.filter(item => (!this.searchText || (item.name && item.name.indexOf(this.searchText) !== -1)) && item.workNumber);
    if (e.target.checked) {
      persons.forEach(item => {
        this.usersMap.set(item.memberId, item);
      });
    } else {
      persons.forEach(item => {
        this.usersMap.delete(item.memberId);
      });
    }
  };
  @action initStore = (params: object) => {
    Object.keys(params).forEach(key => {
      if (key === "selected") {
        this.setSelected(params[key]);
      } else {
        this[key] = params[key];
      }
    });
  };

  @action setSelected = (persons: Array<IUser>) => {
    this.usersMap.clear();
    persons.forEach(item => {
      this.usersMap.set(item.memberId, item);
    });
  };

  @action onSelectChange = (rowData: IUser) => {
    if (this.isRadio) {
      this.usersMap.clear();
      this.usersMap.set(rowData.memberId, rowData);
    } else {
      if (this.usersMap.has(rowData.memberId)) {
        this.usersMap.delete(rowData.memberId);
      } else {
        this.usersMap.set(rowData.memberId, rowData);
      }
    }
  };

  @action setBreadcrumbsSlice = (index: number) => {
    this.searchText = "";
    this.breadcrumbs = this.breadcrumbs.slice(0, index + 1);
    this.onSlideTreeChange(this.breadcrumbs[this.breadcrumbs.length - 1]);
  };

  @action setBreadcrumbsPush = (row: IOrg) => {
    this.searchText = "";
    this.breadcrumbs.push(row);
    this.onSlideTreeChange(row);
  };

  @action onSearchSubmit = (value: string) => {
    this.breadcrumbs = this.breadcrumbs.slice(0, 1);
    this.departments = [];
    choosePersonFetch({ name: value }).then((res: any) => {
      runInAction(() => {
        this.persons = res.result.member;
      });
    });
  };

  @action onSlideTreeChange = (row: IOrg) => {
    this.departments = [];
    this.persons = [];
    choosePersonFetch({ orgId: row.orgId }).then((res: any) => {
      runInAction(() => {
        this.departments = res.result.orgGroup;
        this.persons = res.result.member;
      });
    });
  };

  @action onSubmit = (callback: Function) => {
    callback(this.pathname, this.selected);
  };
}

export default new ChoosePersonStore();
