import { observable, action } from "mobx";
import { getUserInfo } from "@/service/http";

class UserInfoStore {
  @observable baseUserInfo: any;
  @observable userMemberInfo: any;
  @observable userOrgInfo: any;
  @observable userRoleInfo: any;

  @action fetchUserInfo = () => {
    getUserInfo().then((res: any) => {
      if (res && res.code === 0) {
        const { result, success } = res;
        if (success) {
          let { baseUserInfo, userMemberInfo, userOrgInfo, userRoleInfo } = result;
          try {
            this.baseUserInfo = JSON.parse(baseUserInfo);
            this.userMemberInfo = JSON.parse(userMemberInfo)[0];
            this.userOrgInfo = JSON.parse(userOrgInfo)[0];
            this.userRoleInfo = JSON.parse(userRoleInfo)[0];
          } catch (err) {
            throw err;
          }
        }
      }
    });
  };
}

export default new UserInfoStore();
