import wechatAxiosInstance from "../index";
interface IParam {
  accessToken: string;
}
/**
 * "errcode":0,
    "errmsg":"ok",
    "ticket":"bxLdikRXVbTPdHSM05e5u5sUoXNKd8-41ZO3MhKoyN5OfkWITDGgnr2fwJ0m9E8NYzWKVZvdVtaUgWvsdshFKA",
    "expires_in":7200
 */

export default function (): Promise<any> {
  return new Promise((resolve, reject) => {
    wechatAxiosInstance
      .request({
        url: "/doWeChat/weChat/token/getWeChatTicket",
        method: "GET"
      })
      .then((res: any) => {
        resolve(res);
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
}
