import { observable, action, runInAction, computed } from "mobx";
// import { IPicker, IDetail, IKv, ILocation } from '../../interface';
import { pageFileUpload } from "@/service/http";
import { Toast } from "antd-mobile";
import { Item } from "../videoMonitor/components/list";
import LoadingIcon from "@/assets/imgs/loading.gif";
class SelectImageStore {
  @observable title = "查看图片";
  @observable previewed = false;
  @observable curPreviewIndex = 0;
  @observable propsFiles: Array<any> = [];
  @observable isView = true;
  @observable uploading = false;
  @computed get files() {
    return this.propsFiles
      .filter(item => {
        return item.operation === "ADD";
      })
      .map(item => {
        let url;
        const { filepath, fileType } = item;
        if (item.url) {
          url = item.url;
        } else if (fileType === 0) {
          url = `/file/formAtt/${filepath}`;
        } else {
          url = `/api/restApi/pageControl/attachment/${filepath}`;
        }
        return {
          url,
          id: filepath
        };
      });
  }
  @action initStore = (state: any) => {
    const { pics, isView } = state;
    this.isView = !!isView;
    this.propsFiles = [];
    this.propsFiles = pics;
  };

  @action onFileChange = (files: any, operationType: string, index: number) => {
    if (operationType === "add") {
      const file = files[files.length - 1].file;
      if (file.size < 5 * 1024 * 1024) {
        this.propsFiles.push({
          url: LoadingIcon,
          operation: "ADD"
        });
        const i = this.propsFiles.length - 1;
        const formData = new FormData();
        formData.set("name", file.name);
        formData.set("type", file.type);
        formData.set("lastModifiedDate", file.lastModifiedDate);
        formData.set("size", file.size);
        formData.set("file", file);
        this.uploading = true;
        pageFileUpload(formData)
          .then(res => {
            let filePath = res.filePath;
            const fileType = res.filePath.startsWith("/formAtt/") ? 0 : 1;
            const index = res.filePath.lastIndexOf("/");

            filePath = filePath.substring(index + 1);
            this.propsFiles[i] = {
              filename: res.fileName.substring(0, 63),
              filepath: filePath,
              filesize: file.size,
              type: file.type,
              operation: "ADD",
              fileType: fileType,
              uploadSource: "user"
            };
          })
          .catch(err => {
            Toast.info("系统错误，请稍后尝试");
            console.log("上传图片出错", err);
          })
          .finally(() => {
            this.uploading = false;
          });
      } else {
        Toast.info("图片超出5M");
        return;
      }
    } else {
      this.propsFiles.forEach((item, i) => {
        if (item.operation === "DELETE" && i <= index) {
          index += 1;
        }
      });
      this.propsFiles[index].operation = "DELETE";
    }
  };
  // @action BatchDelete = (deleteIndex: []) => {
  //   deleteIndex.forEach((item, index) => {
  //     this.propsFiles.find((obj, index) => {
  //       if (obj.filepath == item) {
  //         this.propsFiles[index].operation = "DELETE";
  //       }
  //     });
  //   });
  // };
}

export default new SelectImageStore();
