import { observable, action, runInAction, computed, toJS } from "mobx";
import http from "@/service/http";
import { IPid, IDropdown, IParams } from "./interface";

class Store {
  allGroupData: any[] = [];
  @observable breadcrumbs: IDropdown[] = [{ id: "root", name: "全部" }];
  @observable dropdowns: IDropdown[] = [];
  @observable pid: IPid | null = { type: "all", id: "root" };
  @observable isLoading: boolean = false;
  @observable showDropdown: boolean = false;

  @action reset = () => {
    this.breadcrumbs = [{ id: "root", name: "全部" }];
    this.dropdowns = [];
    this.pid = null;
    this.isLoading = false;
    this.showDropdown = false;
  };

  @action updatePid = (item?: IDropdown) => {
    if (!item) {
      this.pid = { type: "all", id: "root" };
      return;
    }
    const { extraKeyData } = item;
    const { LocationType } = extraKeyData || {};
    if (Number(LocationType) === 0) {
      this.pid = { type: "groupId", id: item.id };
    } else if (Number(LocationType) === 1) {
      this.pid = { type: "buildId", id: item.id };
    } else if (Number(LocationType) === 2) {
      this.pid = { type: "floorId", id: item.id };
    }
  };

  @action updateBreadcrumbs = (dropdown: IDropdown) => {
    let isHasId = this.breadcrumbs.find(item => item.id === dropdown.id);
    if (isHasId) return;
    let isHasLevelIndex;
    this.breadcrumbs.forEach((item, i) => {
      if (item.extraKeyData && dropdown.extraKeyData && item.extraKeyData.LocationType === dropdown.extraKeyData.LocationType) {
        isHasLevelIndex = i;
      }
    });
    if (isHasLevelIndex) {
      if (this.breadcrumbs[isHasLevelIndex].id === dropdown.pid) {
        this.breadcrumbs.splice(isHasLevelIndex + 1, 0, dropdown);
      } else {
        this.breadcrumbs[isHasLevelIndex] = dropdown;
      }
      return;
    }
    this.breadcrumbs.push(dropdown);
  };

  @action showDropdownChange = (bool?: boolean) => {
    if (bool !== true && bool !== false) {
      this.showDropdown = !this.showDropdown;
    } else {
      this.showDropdown = bool;
    }
  };

  @action updateBreadcrumbsLength = (i: number) => {
    const breadcrumbs = toJS(this.breadcrumbs, {
      recurseEverything: true
    });
    breadcrumbs.length = i;
    this.breadcrumbs = breadcrumbs;
  };

  @action findGroupData = (id: string) => {
    if (this.allGroupData && this.allGroupData.length > 0) {
      let data = this.allGroupData.filter(item => item.pid === id);
      if (data && data.length > 0) {
        this.dropdowns = data;
        return true;
      } else {
        return false;
      }
    } else {
      return false;
    }
  };

  @action fetchData = (params: IParams) => {
    if (this.isLoading) return;
    this.isLoading = true;
    http
      .getGroup(params)
      .then(res => {
        const { result } = res;
        if (params.type === 12) {
          this.allGroupData = result;
          runInAction(() => {
            this.dropdowns = result.filter((item: any) => !item.pid);
          });
        } else {
          runInAction(() => {
            this.dropdowns = result;
          });
        }
      })
      .finally(() => {
        runInAction(() => {
          this.isLoading = false;
        });
      });
  };
}

export default new Store();
