// const wx = require('../../core/jweixin-1.2.0');

interface IParams extends WxEvent {
    localId: string;
}

const API_NAME = 'getLocation';

export const getLocation = (params: IParams) => {
    window.wx.getLocation(params);
};

export default {
    getLocation,
    getName: (): string => {
        return API_NAME;
    },
};
