import http from "@/utils/data-api-service";
import { showTableData } from "@/service/http/common";
import dayjs from "dayjs";
import { removePrefix } from "@/utils/common";

/**
 * 履职完成率排名
 * @param date 月
 */
function performCompletionRate(month: string) {
  const startofMonth = dayjs(month).startOf("month").format("YYYY-MM-DD");
  let endofMonth = "";
  if (dayjs(month, "YYYY-MM") > dayjs().subtract(1, "month")) {
    endofMonth = dayjs().subtract(1, "day").format("YYYY-MM-DD");
  } else {
    endofMonth = dayjs(month).endOf("month").format("YYYY-MM-DD");
  }
  // eslint-disable-next-line prefer-const
  const filterCondition = [
    {
      type: "1",
      field: "view_org_executetask_complete_detail.endTime",
      condition: ">=",
      connection: "&&",
      id: 1,
      pid: 0,
      value: startofMonth
    },
    {
      type: "1",
      field: "view_org_executetask_complete_detail.endTime",
      condition: "<=",
      connection: "&&",
      id: 2,
      pid: 0,
      value: endofMonth
    }
  ];
  // [
  //     dayjs().startOf(month).format('YYYY-MM'),
  //     dayjs().endOf('month').format('YYYY-MM'),
  // ].map((item, i) => {
  //     if (i === 0) {
  //         return {
  //             type: '1',
  //             field: 'view_org_executetask_complete_detail_monthly.endTime',
  //             condition: '>=',
  //             connection: '&&',
  //             id: 1,
  //             pid: 0,
  //             value: item,
  //         };
  //     } else if (i === 1) {
  //         return {
  //             type: '1',
  //             field: 'view_org_executetask_complete_detail_monthly.endTime',
  //             condition: '<=',
  //             connection: '&&',
  //             id: 2,
  //             pid: 1,
  //             value: item,
  //         };
  //     }
  // });
  const requestParam = {
    tableName: "view_org_executetask_complete_detail",
    length: 10,
    start: 0,
    type: "TABLE_QUERY",
    params: {
      orderBy: [
        {
          fieldName: "view_org_executetask_complete_detail_completionRate1",
          sort: "DESC",
          isNeedTableName: false
        }
      ],
      filterCondition
    },
    fieldName: ["view_org_executetask_complete_detail.id", "view_org_executetask_complete_detail.status"],
    statisticsColumn: [
      {
        tableName: "view_org_executetask_complete_detail",
        columnType: "GROUP_BY",
        columnName: "id"
      },
      {
        tableName: "view_org_executetask_complete_detail",
        columnType: "FORMULA",
        columnName: "completionRate1",
        formula:
          "ROUND(COUNT(CASE view_org_executetask_complete_detail.status WHEN '1' THEN 1 ELSE NULL END)/(CASE count(*) WHEN 0 THEN 1 ELSE count(*) END)*100,2)",
        characters: "%",
        formulaColumnsList: ["view_org_executetask_complete_detail.status"]
      },
      {
        tableName: "view_org_executetask_complete_detail",
        columnType: "FORMULA",
        columnName: "incomplete",
        formula: "COUNT(CASE view_org_executetask_complete_detail.status WHEN '2' THEN 1 ELSE NULL END ) ",
        formulaColumnsList: ["view_org_executetask_complete_detail.status"]
      }
    ],
    controlPermissionModelDTOs: []
  };
  return new Promise((resolve, reject) => {
    http
      .request({
        url: "/api/restApi/controlData/showTableData",
        method: "POST",
        data: requestParam
      })
      .then((data: any) => {
        const { result, success } = data;
        if (success) {
          result.aaData = removePrefix("view_org_executetask_complete_detail", result.aaData);
          resolve(result);
        } else {
          reject(new Error("请求出错"));
        }
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
}

/**
 * 履职质量
 * @param month
 */
function performQuality(month: string) {
  const startofMonth = dayjs(month).startOf("month").format("YYYY-MM-DD");
  let endofMonth = "";
  if (dayjs(month, "YYYY-MM") > dayjs().subtract(1, "month")) {
    endofMonth = dayjs().subtract(1, "day").format("YYYY-MM-DD");
  } else {
    endofMonth = dayjs(month).endOf("month").format("YYYY-MM-DD");
  }
  const filterCondition = [
    {
      type: "1",
      field: "view_org_executetask_qulity_detail.endTime",
      condition: ">=",
      connection: "&&",
      id: 1,
      pid: 0,
      value: startofMonth
    },
    {
      type: "1",
      field: "view_org_executetask_qulity_detail.endTime",
      condition: "<=",
      connection: "&&",
      id: 2,
      pid: 0,
      value: endofMonth
    }
  ];
  const param = {
    tableName: "view_org_executetask_qulity_detail",
    start: 0,
    length: 10,
    type: "TABLE_QUERY",
    params: {
      orderBy: [],
      filterCondition
    },
    subQuery: [],
    fieldName: [
      "view_org_executetask_qulity_detail.id",
      "view_org_executetask_qulity_detail.nomal",
      "view_org_executetask_qulity_detail.abnomal",
      "view_org_executetask_qulity_detail.notChecked"
    ],
    statisticsColumn: [
      {
        tableName: "view_org_executetask_qulity_detail",
        columnType: "GROUP_BY",
        columnName: "id"
      },
      {
        tableName: "view_org_executetask_qulity_detail",
        columnType: "SUM",
        columnName: "nomal"
      },
      {
        tableName: "view_org_executetask_qulity_detail",
        columnType: "SUM",
        columnName: "abnomal"
      },
      {
        tableName: "view_org_executetask_qulity_detail",
        columnType: "SUM",
        columnName: "notChecked"
      }
    ],
    controlPermissionModelDTOs: []
  };
  return new Promise((resolve, reject) => {
    http
      .request({
        url: "/api/restApi/controlData/showTableData",
        method: "POST",
        data: param
      })
      .then((data: any) => {
        const { result, success } = data;
        if (success) {
          result.aaData = removePrefix("view_org_executetask_qulity_detail", result.aaData);
          resolve(result);
        } else {
          reject(new Error("请求出错"));
        }
      })
      .catch((err: Error) => {
        reject(err);
      });
  });
}
export { performCompletionRate, performQuality };
