import wechatAxiosInstance from "@/service/wechatAxiosInstance";

interface IResponse {
  access_token: string;
  access_token_expires_in: string | number;
  client_token_expires_in: string | number;
  client_token: string;
  we_chat_token_expires_in: string | number;
  we_chat_token: string;
}
export default function (): Promise<any> {
  return new Promise((resolve, reject) => {
    wechatAxiosInstance
      .request({
        url: "/doWeChat/weChat/access/getWeChatCache",
        method: "GET"
      })
      .then((res: any) => {
        const { data } = res;
        if (data && data.code === 0) {
          resolve(data.result);
        } else {
          reject(data.message);
        }
      })
      .catch((err: Error) => {
        reject(err.toString());
      });
  });
}
